/*
 * don't provide for uncompiled (will result in provide error due
 * to double provision if H-core is included
 */
if (COMPILED) {
  goog.provide('H');
}


/** @suppress {duplicate} */
var H = H || {};


/**
 * This namespace contains classes and functionality for UI elements.
 *
 * @namespace H.ui
 * @publish
 */
goog.provide('H.ui');

goog.require('H.ui.InfoBubble');
goog.require('H.ui.MapSettingsControl');
goog.require('H.ui.Pano');
goog.require('H.ui.ScaleBar');
goog.require('H.ui.UI');
goog.require('H.ui.ZoomControl');
goog.require('H.ui.base');
goog.require('H.ui.util');


/**
 * Function returns build information
 * @return {H.util.BuildInfo}
 * @export
 */
H.ui.buildInfo = function() {
  'use strict';
  return H.util.BuildInfo.get('{BUILD_NAME}', '{BUILD_VERSION}', '{BUILD_REVISION}');
};
