goog.provide('H.ui.util');


/**
 * Flag indicates if we're running in stock android browser
 * @type {boolean}
 */
H.ui.util.isStockAndroid = (function() {
  var ua = navigator.userAgent,
      isAndroidMobile = ua.indexOf('Android') > -1 && ua.indexOf('Mozilla/5.0') > -1 && ua.indexOf('AppleWebKit') > -1,
      regExAppleWebKit = /AppleWebKit\/([\d.]+)/,
      match = regExAppleWebKit.exec(ua),
      appleWebKitVersion = (match === null ? null : parseFloat(regExAppleWebKit.exec(ua)[1]));
  return isAndroidMobile && appleWebKitVersion !== null && appleWebKitVersion < 537;
}());
