/**
 * Namespace contaisn functionality related to internationalization.
 * @namespace H.ui.i18n
 * @publish
 */
goog.provide('H.ui.i18n');


/**
 * Default available locales. UI provides default translations for
 * this set of locale codes.
 * @type {Array.<string>}
 * @export
 * @publish
 */
H.ui.i18n.defaultLocales = [];


/**
 * Set of default translation maps
 * @type {Object}
 */
H.ui.i18n.defaultStrings = {};


/**
 * Defines minimum key set required for external
 * maps
 * @type {Array.<string>}
 */
H.ui.i18n.minimumKeySet = [];


/**
 * This method sets default translation map
 * used by default translations. Do not publish!
 * @param {!string} locale
 * @param {!Object} map
 */
H.ui.i18n.define = function(locale, map) {
  'use strict';
  H.ui.i18n.defaultStrings[locale] = map;
  if (H.ui.i18n.defaultLocales.indexOf(locale) === -1) {
    H.ui.i18n.defaultLocales.push(locale);
  }

  if (!H.ui.i18n.minimumKeySet.length) {
    H.ui.i18n.minimumKeySet = Object.keys(map);
  } else {
    H.ui.i18n.validateMap(locale, map);
  }
};


/**
 * This method returns a bollean value indicating whether the UI supports
 * the locale by default.
 * @param {string} locale the locale code to be queried
 * @return {boolean} {@code true} if the locale is supported, otherwise {@code false}.
 */
H.ui.i18n.hasDefaultLocale = function(locale) {
  'use strict';
  return H.ui.i18n.defaultLocales.indexOf(locale) >= 0;
};


/**
 * This method validates provided translation map. It
 * returns false if map has less number of matching keys than
 * default minimum key set. Do not publish!
 * @param {string} locale
 * @param {Object} map
 */
H.ui.i18n.validateMap = function(locale, map) {
  'use strict';
  var minimumKeySet = H.ui.i18n.minimumKeySet,
      i = minimumKeySet.length,
      key;
  while (i--) {
    key = minimumKeySet[i];
    if (!map[key]) {
      throw new Error('\'' + key + '\' key missing in translation map for ' + locale);
    }
  }
};


