goog.provide('H.ui.panorama.Miniman');

goog.require('H.ui.css');
goog.require('H.ui.icons.pano');



/**
 * This class encapsulates the miniman attached to the pointer when discovering street level entry points.
 * @constructor
 * @extends {H.ui.base.Container}
 */
H.ui.panorama.Miniman = function() {
  'use strict';
  H.ui.base.Container.call(this, 'div', 'H_pano_mm');

  /**
   * Package internal property that holds miniman's circle DOM element
   * @type {H.ui.base.Element}
   */
  this.circleEl = new H.ui.base.Element('div', 'H_pano_circle H_noevs');

  /**
   * Package internal property that holds miniman DOM element
   * @type {H.ui.base.Element}
   */
  this.minimanEl = new H.ui.base.Element('div', 'H_pano_man H_noevs');

  this.addChild(this.circleEl);
  this.addChild(this.minimanEl);
};
H.lang.inherits(H.ui.panorama.Miniman, H.ui.base.Container);


/**
 * @override
 * @this {H.ui.panorama.Miniman}
 */
H.ui.panorama.Miniman.prototype['renderInternal'] = function(element, doc) {
  //renderInternal must be used with square bracket notation because the class is not
  //exported
  'use strict';

  var offscreen = -1000;
  H.ui.base.Container.prototype['renderInternal'].call(this, element, doc);

  // render circle and miniman off screen their positino will be set on mouse move
  H.ui.css.translate(this.minimanEl['el'], offscreen, offscreen);
  H.ui.css.translate(this.circleEl['el'], offscreen, offscreen);

  this.circleEl['el'].innerHTML = H.ui.icons.pano.man_circle;
  this.setPanorama(false);
};


/**
 * Sets the screen position of the miniman
 * @param {number} x the x-coordinate
 * @param {number} y the y-coordinate
 */
H.ui.panorama.Miniman.prototype.setScreenPosition = function(x, y) {
  'use strict';
  H.ui.css.translate(this.minimanEl['el'], x, y);
  H.ui.css.translate(this.circleEl['el'], x, y);
};


/**
 * Notifies the miniman of either a panorama being available at a screen position, or
 * no panorama availability.
 * @param {boolean} isAvailable a flag indicating whether a panorama is available
 * @param {number=} opt_x the x-coordinate if a panorama is available
 * @param {number=} opt_y the y-coordinate if a panorama is available
 */
H.ui.panorama.Miniman.prototype.setPanorama = function(isAvailable, opt_x, opt_y) {
  'use strict';
  this.minimanEl['el'].innerHTML = isAvailable ? H.ui.icons.pano.man_armup : H.ui.icons.pano.man_armdown;
  if (isAvailable) {
    H.ui.css.translate(this.circleEl['el'], opt_x || 0, opt_y || 0);
  }
  this.setDisabled(!isAvailable);
};
