goog.provide('H.ui.traffic.TrafficIncidents');

goog.require('H.ui.InfoBubble');



/**
 * This class represents the traffic incidents functionality accessible from the map settings control.
 * It consists of a button (which is added to the map settings control) as well as functionality for
 * showing info bubbles when clicking on markers shown by the traffic incidents layer.
 * @constructor
 */
H.ui.traffic.TrafficIncidents = function() {
  'use strict';

  /**
   * @private
   * @type {function(H.util.Event)}
   */
  this.onIncidentTap_ = H.lang.bind(this.onIncidentTap_, this);

  /**
   * @private
   * @type {function(H.util.Event)}
   */
  this.onIncidentsStateChange_ = H.lang.bind(this.onIncidentsStateChange_, this);

  /**
   * @type {H.ui.base.PushButton}
   * @private
   */
  this.incidentsBtn_ = new H.ui.base.PushButton({
    'label': 'Show traffic incidents',
    'onStateChange': this.onIncidentsStateChange_
  });

  /**
   * @type {H.ui.InfoBubble}
   * @private
   */
  this.incidentsBubble_ = null;

  /**
   * @type {number}
   * @private
   */
  this.bubbleMin_ = 0;

  /**
   * @type {number}
   * @private
   */
  this.bubbleMax_ = 22;
};


/**
 * @private
 * @type {string}
 */
H.ui.traffic.TrafficIncidents.INFOBUBBLE_TEMPLATE_ =
    '<div class="H_tib">' +
    '<p>{{title}}</p>' +
    '<p class="H_tib_desc">{{desc}}</p>' +
    '<p class="H_tib_time">' +
    '<span class="H_tib_left">{{traffic.from}}</span><span class="H_tib_right">{{from}}</span><br/>' +
    '<span class="H_tib_left">{{traffic.until}}</span><span class="H_tib_right">{{until}}</span></p>' +
    '</div>';


/**
 * Retrieves the traffic incidents button (for the map settings control)
 * @return {H.ui.base.PushButton}
 */
H.ui.traffic.TrafficIncidents.prototype.getButton = function() {
  'use strict';
  return this.incidentsBtn_;
};


/**
 * Sets the traffic incidents layer to be shown when activating the respective button
 * on the map settings control.
 * @param {H.map.layer.Layer} incidentsLayer the incidents layer
 */
H.ui.traffic.TrafficIncidents.prototype.setIncidentsLayer = function(incidentsLayer) {
  'use strict';
  var provider,
      oldIncidentsLayer = this.incidentsBtn_.getData(),
      newIncidentsLayer = incidentsLayer;

  if (oldIncidentsLayer) {
    oldIncidentsLayer.getProvider().removeEventListener('tap', this.onIncidentTap_);
  }

  if (newIncidentsLayer) {
    provider = newIncidentsLayer.getProvider();
    provider.addEventListener('tap', this.onIncidentTap_);
    this.bubbleMin_ = provider['min'];
    this.bubbleMax_ = provider['max'];
    this.incidentsBtn_.setData(newIncidentsLayer);
    this.incidentsBtn_.setVisibility(true);
    this.updateButtonState();
  } else {
    this.incidentsBtn_.setData(null);
    this.incidentsBtn_.setVisibility(false);
    this.bubbleMin_ = 0;
    this.bubbleMax_ = 22;
  }
};


/**
 * Sets the reference to the UI (for adding bubbles)
 * @param {H.ui.UI} ui
 */
H.ui.traffic.TrafficIncidents.prototype.setUi = function(ui) {
  'use strict';
  var that = this;
  this.ui_ = ui;
  this.incidentsBtn_.setLabel(ui.getLocalization().translate('layer.incidents'));
  this.updateButtonState();
  this.ui_.getMap().addEventListener('mapviewchange', function(ev) {
    var zoom = ev['newValue'].zoom;
    if (that.incidentsBubble_ && (ev['modifiers'] & ev['ZOOM'])) {
      if (zoom < that.bubbleMin_ || zoom > that.bubbleMax_) {
        that.incidentsBubble_.close();
      }
    }
  });
};


/**
 * Synchronizes the traffic incidents button state with the map's data model.
 */
H.ui.traffic.TrafficIncidents.prototype.updateButtonState = function() {
  'use strict';
  //synchronize UI if traffic incidents layer is already in the map's layers
  var ui = this.ui_,
      incidentsLayer = this.incidentsBtn_.getData(),
      map;
  if (ui && (map = ui.getMap()) && incidentsLayer) {
    if (map.getLayers().indexOf(incidentsLayer) >= 0) {
      this.incidentsBtn_.setState(H.ui.base.Button.State.DOWN, true);
    } else {
      this.incidentsBtn_.setState(H.ui.base.Button.State.UP, true);
      this.incidentsBubble_ && this.incidentsBubble_.close();
    }
  } else {
    this.incidentsBtn_.setState(H.ui.base.Button.State.UP, true);
  }
};


/**
 * Handler for state changes of the incidents button
 * @param {H.util.Event} evt the event signaling the state change
 * @private
 */
H.ui.traffic.TrafficIncidents.prototype.onIncidentsStateChange_ = function(evt) {
  'use strict';

  var map = this.ui_.getMap(),
      incidentsLayer = /** @type {H.map.layer.Layer} */(this.incidentsBtn_.getData());
  if (map && incidentsLayer) {
    if (this.incidentsBtn_.getState() === H.ui.base.Button.State['DOWN']) {
      map.addLayer(incidentsLayer);
      //incidentsLayer.getProvider().addEventListener('tap', this.onIncidentTap_);
    } else {
      map.removeLayer(incidentsLayer);
      //incidentsLayer.getProvider().removeEventListener('tap', this.onIncidentTap_);
    }
  }
};


/**
 * Handles taps on traffic incidents (by showing an info bubble with details).
 * @param {H.util.Event} evt the tap event
 * @private
 */
H.ui.traffic.TrafficIncidents.prototype.onIncidentTap_ = function(evt) {
  'use strict';
  var that = this,
      data,
      template = H.ui.traffic.TrafficIncidents.INFOBUBBLE_TEMPLATE_,
      incidentType;

  if (evt.target && (data = evt.target.getData())) {
    if (!this.incidentsBubble_) {
      this.incidentsBubble_ = new H.ui.InfoBubble(evt.target.getPosition());
      this.ui_.addBubble(this.incidentsBubble_);
    }
    this.incidentsBubble_.setPosition(evt.target.getPosition());

    incidentType = data['TRAFFIC_ITEM_TYPE_DESC'].replace(' ', '_');
    if (this.ui_.getLocalization().hasKey('traffic.' + incidentType)) {
      incidentType = this.ui_.getLocalization().translate('traffic.' + incidentType);
    }

    this.incidentsBubble_.setContent(
        template.replace('{{title}}', incidentType)
                .replace('{{desc}}', data['TRAFFIC_ITEM_DESCRIPTION'][0]['value'])
                .replace('{{traffic.from}}', this.ui_.getLocalization().translate('traffic.from'))
                .replace('{{traffic.until}}', this.ui_.getLocalization().translate('traffic.until'))
                .replace('{{from}}', data['START_TIME'])
                .replace('{{until}}', data['END_TIME']));
    this.incidentsBubble_.open();
  }

  evt.stopPropagation();
};
