goog.provide('H.ui.UI');

goog.require('goog.events.EventTarget');
goog.require('H.ui.Layout');
goog.require('H.ui.UnitSystem');
goog.require('H.ui.dom');
goog.require('H.ui.i18n.Localization');



/**
 * This class encapsulates map UI functionality.
 *
 * @constructor
 * @param {H.Map} map
 * @param {H.ui.UI.Options=} opt_options
 * @extends {goog.events.EventTarget}
 * @implements {H.util.ICapturable}
 *
 * @publish
 * @export
 */
H.ui.UI = function(map, opt_options) {
  'use strict';
  var parentElement = map.getElement(),
      that = this;

  goog.events.EventTarget.call(this);

  /**
   * @type {H.Map}
   * @private
   */
  this.map_ = map;

  /**
   * @type {Document}
   * @private
   */
  this.doc_ = parentElement.ownerDocument;

  /**
   * @dict
   * @private
   */
  this.controls_ = {};


  /**
   * @type  {Array.<H.ui.InfoBubble>}
   * @private
   */
  this.bubbles_ = [];

  /**
   * @type {Element}
   * @private
   */
  this.el_ = H.ui.dom.mkEl(this.doc_, 'div', 'H_ui');

  /**
   * @type {function(H.util.Event)}
   * @private
   */
  this.onControlAlignmentChanged_ = H.lang.bind(function(evt) {
    this.layout_.put(evt.target, evt.target.getAlignment());
  }, this);
  this.addEventListener('alignmentchange', this.onControlAlignmentChanged_);

  this.onUnitSystemToggleRequest_ = H.lang.bind(function(evt) {
    this.toggleUnitSystem();
  }, this);
  this.addEventListener('toggleunitsystem', this.onUnitSystemToggleRequest_);

  this.onEngineChange_ = H.lang.bind(function(evt) {
    var engineType = this.map_.getEngine().type;
    if (engineType !== this.engineType_) {
      this.engineType_ = engineType;
      this.onEngineTypeChange_(this.engineType_);
    }
  }, this);
  this.map_.addEventListener('enginechange', this.onEngineChange_);

  /**
   * Preventing default for contextmenu and hold
   */
  this.el_.addEventListener('contextmenu', this['prevDefault'], false);
  this.el_.addEventListener('MSHoldVisual', this['prevDefault'], false);

  /**
   * @type {H.ui.Layout}
   * @private
   */
  this.layout_ = new H.ui.Layout(this.el_, this.doc_);

  /**
   * @type {H.ui.UnitSystem}
   * @private
   */
  this.unitSystem_ = H.ui.UnitSystem['METRIC'];

  this.setLocale_('en-US');

  /**
   * @type {H.Map.EngineType}
   * @private
   */
  this.engineType_ = this.map_.getEngine().type;

  if (opt_options) {
    this.processOptions_(opt_options);
  }

  //finally append this UI's HTML element to the viewport element
  parentElement.appendChild(this.el_);

  setTimeout(function() {
    that.layout_.update();
  }, 1);
};
H.lang.inherits(H.ui.UI, goog.events.EventTarget);


/**
 * @private
 * @param {Event} evt
 */
H.ui.UI.prototype['prevDefault'] = function(evt) {
  // Fix for MAPSJS-361 (don't prevent contextmenu for info bubble content)
  var curr = evt.target;
  while (curr && !/\bH_ib_content\b/.test(curr.className)) {
    curr = curr.parentNode;
  }
  if (!curr) {
    evt.preventDefault();
  }
};


/**
 * This method returns this ui's root element.
 * @return {Element} the root element
 *
 * @export
 * @publish
 */
H.ui.UI.prototype.getElement = function() {
  'use strict';
  return this.el_;
};


/**
 * Returns the map instance to which this UI was added.
 * @return {H.Map}
 *
 * @export
 * @publish
 */
H.ui.UI.prototype.getMap = function() {
  'use strict';
  return this.map_;
};


/**
 * This method returns this UI object's current unit system.
 * @return {H.ui.UnitSystem} the current unit system
 */
H.ui.UI.prototype.getUnitSystem = function() {
  'use strict';

  return this.unitSystem_;
};


/**
 * This method sets this UI object's unit system for displaying distances.
 * @param {H.ui.UnitSystem} unitSystem the unit system to use
 * @export
 * @publish
 */
H.ui.UI.prototype.setUnitSystem = function(unitSystem) {
  'use strict';

  var currentUnitSystem = this.unitSystem_,
      controls = this.controls_,
      key;
  if (unitSystem !== this.unitSystem_) {
    this.unitSystem_ = unitSystem;
    for (key in controls) {
      if (controls[key] instanceof H.ui.Control) {
        controls[key]['onUnitSystemChange'](this.getUnitSystem());
      }
    }
  }
};


/**
 * @private
 * @param {string|H.ui.i18n.Localization} locale the locale to be used
 */
H.ui.UI.prototype.setLocale_ = function(locale) {
  'use strict';

  var localeCode,
      localization;

  if (goog.isString(locale)) {
    localeCode = locale;
  } else if (locale instanceof H.ui.i18n.Localization) {
    localeCode = locale.getLocale();
    localization = locale;
  } else {
    throw new Error('The locale parameter must be a string or a H.ui.i18n.Localization object.');
  }

  if (localeCode) {
    if (localization) {
      this.localization_ = localization;
    } else {
      if (H.ui.i18n.hasDefaultLocale(localeCode)) {
        this.localization_ = new H.ui.i18n.Localization(localeCode);
      } else {
        throw new Error('Locale [' + localeCode + '] is not supported.');
      }
    }
  } else {
    throw new Error('No locale was defined.');
  }
};


/**
 * This method returns the UI's current localization object
 * @return {H.ui.i18n.Localization} the localization object
 */
H.ui.UI.prototype.getLocalization = function() {
  'use strict';
  return this.localization_;
};


/**
 * Informs all current control that the map's engine changed.
 * @param {H.Map.EngineType} engineType
 * @private
 */
H.ui.UI.prototype.onEngineTypeChange_ = function(engineType) {
  var controls = this.controls_,
      key;
  for (key in controls) {
    if (controls[key] instanceof H.ui.Control) {
      controls[key]['onMapEngineTypeChange'](engineType);
    }
  }
  this.layout_.update();
};


/**
 * Toggles this UI's unit system between {@link H.ui.UnitSystem.METRIC} and {@link H.ui.UnitSystem.IMPERIAL}.
 *
 * @export
 * @publish
 */
H.ui.UI.prototype.toggleUnitSystem = function() {
  'use strict';

  if (this.unitSystem_ === H.ui.UnitSystem['METRIC']) {
    this.setUnitSystem(H.ui.UnitSystem['IMPERIAL']);
  } else {
    this.setUnitSystem(H.ui.UnitSystem['METRIC']);
  }
};


/**
 * This method adds an info bubble to the UI.
 * @param {H.ui.InfoBubble} bubble the info bubble to be added
 *
 * @export
 * @publish
 */
H.ui.UI.prototype.addBubble = function(bubble) {
  'use strict';
  var nBubbles = this.bubbles_.length,
      uiEl = this.getElement(),
      bubbleEl,
      nextSibling;

  if (this.bubbles_.indexOf(bubble) < 0) {
    bubble.setParentEventTarget(this);
    bubble.setMap(this.map_);
    bubbleEl = bubble.render(this.doc_);
    // FIX for MAPSJS-73:
    // We need to insert the bubble before the control layout elements but after the last bubble.
    // So if we already have bubbles we insert before the last bubble's next sibling. Otherwise
    // we insert before the first child (which is the first control layout anchor).
    if (nBubbles > 0) {
      nextSibling = this.bubbles_[nBubbles - 1]['el'].nextSibling;
    } else {
      nextSibling = this.getElement().firstChild;

    }
    uiEl.insertBefore(bubbleEl, nextSibling);

    this.bubbles_.push(bubble);
  }
};


/**
 * This method removes a previously added info bubble from the UI.
 * @param {H.ui.InfoBubble} bubble the info bubble to be removed
 *
 * @publish
 * @export
 */
H.ui.UI.prototype.removeBubble = function(bubble) {
  'use strict';

  var index = this.bubbles_.indexOf(bubble);
  if (index > -1) {
    bubble.setParentEventTarget(null);
    bubble.setMap(null);
    this.bubbles_.splice(index, 1);
    this.getElement().removeChild(bubble.getElement());
  }
};


/**
 * This method returns a list of info bubble objects which are currently attached to this UI.
 * @return {Array.<H.ui.InfoBubble>} the list of info bubbles
 *
 * @publish
 * @export
 */
H.ui.UI.prototype.getBubbles = function() {
  'use strict';

  return this.bubbles_;
};


/**
 * This method appends a control to the UI.
 * @param {string} name the name under which to register this control
 * @param {H.ui.Control} control the control to add to this UI
 *
 * @publish
 * @export
 */
H.ui.UI.prototype.addControl = function(name, control) {
  'use strict';

  if (!this.getControl(name)) {
    control.setParentEventTarget(this);
    control.setMap(this.map_);
    control.setLocalization(this.localization_);
    control.render(this.doc_);
    control['onUnitSystemChange'](this.unitSystem_);
    control['onMapEngineTypeChange'](this.map_.getEngine().type);
    this.controls_[name] = control;
  } else {
    throw new Error('A control with name [' + name + '] is already registered.');
  }
};


/**
 * Removes a previously registered control from the UI object.
 * @publish
 * @export
 * @param {string} name the name under which this control was previously registered
 * @return {H.ui.Control} the removed control
 */
H.ui.UI.prototype.removeControl = function(name) {
  'use strict';

  var control = this.getControl(name);
  if (control) {
    control.setParentEventTarget(null);
    control.setMap(null);
    control.remove();
    delete this.controls_[name];
  } else {
    throw new Error('Control [' + name + '] not found in this UI\'s controls.');
  }
  return control;
};


/**
 * This method returns a UI control which was previously registered with the provided name.
 * @param {string} name the name under which the control was registered.
 * @return {H.ui.Control} the control or {@code undefined} if the control does not exist.
 * @export
 * @publish
 */
H.ui.UI.prototype.getControl = function(name) {
  'use strict';

  return this.controls_[name];
};


/**
 * @override
 */
H.ui.UI.prototype.disposeInternal = function() {
  var controlKey,
      parentNode;

  //remove controls
  for (controlKey in this.controls_) { this.controls_[controlKey].dispose(); }

  //dispose all bubbles
  this.getBubbles().forEach(function(bubble) { bubble.dispose(); });

  //remove element
  parentNode = this.el_.parentNode;
  if (parentNode) { parentNode.removeChild(this.el_); }

  //dispose of event target
  goog.events.EventTarget.prototype.disposeInternal.call(this);
};


/**
 * Disposes of this UI object including currently open info bubbles and controls attached
 * to the UI.
 * @override
 * @export
 * @publish
 */
H.ui.UI.prototype.dispose = function() {
  goog.events.EventTarget.prototype.dispose.call(this);
};


/**
 * This method processes UI options passed to the UI's constructor.
 * @param {H.ui.UI.Options} options the UI options
 * @private
 */
H.ui.UI.prototype.processOptions_ = function(options) {
  'use strict';

  var controlOptions,
      locale;
  if (options['unitSystem']) {
    this.setUnitSystem(options['unitSystem']);
  }

  if ((locale = options['locale'])) {
    this.setLocale_(locale);
  }

  if ((controlOptions = options['zoom'])) {
    this.addControl(
        'zoom',
        new H.ui.ZoomControl(typeof controlOptions === 'object' ? controlOptions : null));
  }
  if ((controlOptions = options['mapsettings'])) {
    this.addControl(
        'mapsettings',
        new H.ui.MapSettingsControl(typeof controlOptions === 'object' ? controlOptions : null));
  }
  if ((controlOptions = options['scalebar'])) {
    this.addControl(
        'scalebar',
        new H.ui.ScaleBar(typeof controlOptions === 'object' ? controlOptions : null));
  }
  if ((controlOptions = options['panorama']) && H.map.render.panorama) {
    this.addControl(
        'panorama',
        new H.ui.Pano(typeof controlOptions === 'object' ? controlOptions : null));
  }
};


/**
 * @override
 * @export
 */
H.ui.UI.prototype.capture = function(canvas, pixelRatio, callback, opt_errback) {
  var scaleBar = this.getControl('scalebar'),
      ctx = canvas.getContext('2d'),
      canvasWidth = canvas.width,
      canvasHeight = canvas.height,
      padding = 5 * pixelRatio;

  // If ScaleBar is visible, call capture method of it
  if (scaleBar.getVisibility()) {
    scaleBar.capture(pixelRatio, function(scaleBarCanvas) {
      // Draw ScaleBar on the input canvas
      // As ScaleBar is the only capturable element we just render on the bottom-right corner of the canvas,
      // perhaps later, makes sense to take into account its alignment
      ctx.drawImage(scaleBarCanvas,
          canvasWidth - scaleBarCanvas.width - padding, canvasHeight - scaleBarCanvas.height - padding);

      callback(canvas);
    }, opt_errback);
  } else {
    // Otherwise call callback without modifying passed canvas
    callback(canvas);
  }
};


/**
 * This function creates the default UI including the zoom control, map settings control and scalebar and panorama
 * discovery control. The default controls will be assigned the following values:
 *
 *  - Zoom control:
 *      * id: 'zoom'
 *      * alignment: 'right-middle'
 *  - Map settings control:
 *      * id: 'mapsettings'
 *      * alignment: 'bottom-right'
 *  - Scalebar:
 *      * id: 'scalebar'
 *      * alignment: 'bottom-right'
 *  - Pano:
 *      * id: 'panorama'
 *      * alignment: 'top-right'
 *
 * @publish
 * @export
 * @param {H.Map} map The map instance to which to append the UI
 * @param {Object.<H.service.MapType>} mapTypes The map types to use
 * @param {H.ui.i18n.Localization|string=} opt_locale the language to use (or a full localization object).
 * @return {H.ui.UI} the UI instance configured with the default controls
 */
H.ui.UI.createDefault = function(map, mapTypes, opt_locale) {
  'use strict';
  var mapTypeEntries = [];

  if (mapTypes['normal']) mapTypeEntries.push({'name': 'layers.normal', 'mapType': mapTypes['normal']});
  if (mapTypes['satellite']) mapTypeEntries.push({'name': 'layers.satellite', 'mapType': mapTypes['satellite']});
  if (mapTypes['terrain']) mapTypeEntries.push({'name': 'layers.terrain', 'mapType': mapTypes['terrain']});
  if (!mapTypeEntries.length) {
    throw new Error('The mapTypes parameter does not contain any known map types.');
  }

  return new H.ui.UI(
      map,
      /** @type {H.ui.UI.Options} */({
        'unitSystem': H.ui.UnitSystem['METRIC'],
        'zoom': {
          'alignment': H.ui.LayoutAlignment.RIGHT_MIDDLE
        },
        'mapsettings': {
          'alignment': H.ui.LayoutAlignment.BOTTOM_RIGHT,
          'entries': mapTypeEntries,
          'incidents': mapTypes['incidents']
        },
        'scalebar': {
          'alignment': H.ui.LayoutAlignment.BOTTOM_RIGHT
        },
        'panorama': {
          'alignment': H.ui.LayoutAlignment.TOP_RIGHT,
          'mapTypes': mapTypes
        },
        'locale': opt_locale || 'en-US'
      }));
};


/**
 * Optional parameters to be passed to the UI constructor.
 *
 * @typedef {{
 *   unitSystem: (H.ui.UnitSystem|undefined),
 *   zoom: (H.ui.ZoomControl.Options|boolean|undefined),
 *   mapsettings: (H.ui.MapSettingsControl.Options|boolean|undefined),
 *   scalebar: (H.ui.ScaleBar.Options|boolean|undefined),
 *   panorama: (H.ui.Pano.Options|boolean|undefined),
 *   locale: (H.ui.i18n.Localization|string|undefined)
 * }}
 *
 * @publish
 * @property {H.ui.UnitSystem=} unitSystem
 *           An optional unit system to be used by the UI, default is {@link H.ui.UnitSystem.METRIC}
 * @property {(H.ui.ZoomControl.Options|boolean)=} zoom
 * @property {(H.ui.MapSettingsControl.Options|boolean)=} mapsettings
 * @property {(H.ui.ScaleBar.Options|boolean)=} scalebar
 * @property {(H.ui.Pano.Options|boolean)=} panorama
 * @property {(H.ui.i18n.Localization|string)=} locale defines language in which UI
 *  can be rendered. It can be predefined {@link H.ui.i18n.Localization} object
 *  with custom translation map, or a string one of following
 *  'en-US', 'de-DE', 'es-ES', 'fi-FI', 'fr-FR', 'it-IT', 'nl-NL', 'pl-PL',
 *  'pt-BR', 'pt-PT', 'ru-RU', 'tr-TR', 'zh-CN'. If not defined ui will use
 *  {@code 'en-US'} by default
 */
H.ui.UI.Options;
