goog.provide('H.ui.dom');

goog.require('H.ui.util.Events');


/**
 * This methods is a helper method which creates a HTML element with the specified options
 * inside the document to which the UI was attached.
 *
 * @param {Document} doc the document within which to create the element
 * @param {string} type the type of element (e.g. 'div', 'span', etc.).
 * @param {?string=} opt_className an optional class name for the element
 * @param {?string=} opt_innerHTML an optional innerHTML string for the element
 * @param {?string=} opt_cssText an optional cssText to be applied to the element as inline style
 * @return {Element} the created HTMLElement
 */
H.ui.dom.mkEl = function(doc, type, opt_className, opt_innerHTML, opt_cssText) {
  'use strict';

  var el = doc.createElement(type);

  if (opt_className) {
    el.className = opt_className;
  }
  if (opt_innerHTML) {
    el.innerHTML = opt_innerHTML;
  }
  if (opt_cssText) {
    el.style.cssText = opt_cssText;
  }

  return el;
};


/**
 * This method appends an arbitrary number of child elements to the specified parent.
 * @param {Element} parent
 * @param {...Element} var_args
 * @return {Element} returns the parent HTMLElement
 *
 * @example
 * ui.append(aParentElement, aChildElement, anotherChildElement, yetAnotherChildElement)
 */
H.ui.dom.append = function(parent, var_args) {
  'use strict';

  var i = 1,
      l = arguments.length;
  for (; i < l; i++) {
    parent.appendChild(arguments[i]);
  }

  return parent;
};


/**
 * @type {H.ui.util.Events}
 * @private
 */
H.ui.dom.events_ = new H.ui.util.Events();


/**
 * This method adds event listeners to the provided element. It uses the event sets which are
 * currently registered in this event helper.
 * @param {Element} el the element to which to append the event listener
 * @param {string} type the event type to listen for
 * @param {function(Event)} listener the listener function
 * @param {boolean=} opt_useCapture optional flag to indicate whether to listen in the capture phase or
 *  the bubbling phase
 */
H.ui.dom.on = function(el, type, listener, opt_useCapture) {
  'use strict';

  H.ui.dom.events_.addEventListener(el, type, listener, opt_useCapture);
};


/**
 * This method removes previously added event listeners from the provided element. It uses the event sets which are
 * currently registered in this event helper.
 * @param {Element} el the element from which to remove the event listener
 * @param {string} type the event type for which to remove the listener
 * @param {function(Event)} listener the listener function to be remove
 * @param {boolean=} opt_useCapture optional flag indicating whether the listener was added to the capture
 *  or bubbling phase
 */
H.ui.dom.off = function(el, type, listener, opt_useCapture) {
  'use strict';

  H.ui.dom.events_.removeEventListener(el, type, listener, opt_useCapture);
};
