goog.provide('H.ui.css');

goog.require('H.ui.util.VendorPrefix');


/**
 * @private
 * @type {H.ui.util.VendorPrefix}
 */
H.ui.css.prefixes_ = new H.ui.util.VendorPrefix();


/**
 * Sets transform: translate(x, y) on the provided element
 * @param {Element} el the element to translate
 * @param {number} x the x-coordinate of the translation
 * @param {number} y the y-coordinate of the tanslateion
 */
H.ui.css.translate = function(el, x, y) {
  var prop = H.ui.css.prefixes_.getPropertyName('transform');
  el.style[prop] = 'translate(' + x + 'px,' + y + 'px)';
};
