goog.provide('H.ui.base.Element');

goog.require('goog.events.EventTarget');
goog.require('H.ui.dom');



/**
 * This class represents the base class of UI elements such as Buttons, ListEntries, etc.
 *
 * @constructor
 * @param {string=} opt_elementType the type of HTML element this UI element renders as, default is {@code 'div'}
 * @param {string=} opt_className an optional class name to be used on this element
 *
 * @extends {goog.events.EventTarget}
 *
 * @export
 * @publish
 */
H.ui.base.Element = function(opt_elementType, opt_className) {
  'use strict';

  goog.events.EventTarget.call(this);

  this.type_ = opt_elementType || 'div';
  this.classNames_ = opt_className ? opt_className.split(' ') : [];

  //initialize this element's fields
  this.setDisabled(false);
  this.setData(null);
  this.setElement_(null);
};
H.lang.inherits(H.ui.base.Element, goog.events.EventTarget);


/**
 * @enum {string}
 */
H.ui.base.Element.EventType = {
  /**
   * This event is dispatched when Element state is was modified.
   * @event H.ui.base.Element#statechange
   * @type {H.util.Event}
   */
  STATECHANGE: 'statechange'
};


/**
 * This method is called internally by the UI system to invoke the rendering of the
 * UI element.
 * @param {Document} doc the document object into which to render
 * @return {Element} this UI element instance
 */
H.ui.base.Element.prototype.render = function(doc) {
  'use strict';

  if (!doc) {
    throw new Error('Wrong document object');
  }

  //if the incoming UI is not the same as the current one
  this.setElement_(H.ui.dom.mkEl(doc, this.type_, this.classNames_.join(' ')));

  //call this element's render function (implemented by deriving classes)
  this['renderInternal'](this['el'], doc);

  // Update this element's state
  this.setDisabled(this.isDisabled(), true);
  this.setTooltip(this.getTooltip());
  this.setVisibility(this.visibility_);

  return this.getElement();
};


/**
 * This method is the concrete implementation of the UI element. This method receives the pre-rendered HTML
 * element which may be modified by deriving classes.
 * @param {Element} element this UI element's HTML representation
 * @param {Document} doc the HTML document into which the element is currently being rendered
 * @publish
 * @export
 */
H.ui.base.Element.prototype.renderInternal = function(element, doc) {};


/**
 * This method returns this UI element's disabled state as a boolean value.
 * @return {boolean} {@code true} if the element is disabled, {@code false} otherwise
 * @export
 * @publish
 */
H.ui.base.Element.prototype.isDisabled = function() {
  'use strict';

  return this.disabled_;
};


/**
 * This method set's the disabled state of this UI element.
 * @param {boolean} disabled {@code true} to disabled the element, {@code false} to enabled it
 * @param {boolean=} opt_force an optional boolean flag indicating that the value should be set and
 *  propagated even if it is the same as the current state
 * @return {H.ui.base.Element} this element instance
 * @export
 * @publish
 */
H.ui.base.Element.prototype.setDisabled = function(disabled, opt_force) {
  'use strict';

  if (disabled !== this.disabled_ || opt_force) {
    this.disabled_ = disabled;

    if (disabled) {
      this.addClass('H_disabled');
    } else {
      this.removeClass('H_disabled');
    }
  }
  return this;
};


/**
 * This method returns a previously stored arbitrary data from this element.
 * @return {*} the previously stored data object or {@code null} if not data was stored.
 * @export
 * @publish
 */
H.ui.base.Element.prototype.getData = function() {
  'use strict';

  return this.data_;
};


/**
 * This method stores arbitrary data with this UI element.
 * @param {*} data the data to be stored
 * @export
 * @publish
 */
H.ui.base.Element.prototype.setData = function(data) {
  'use strict';

  this.data_ = data;
};


/**
 * This method returns the tool tip of this element.
 * @return {string} the current tool tip
 */
H.ui.base.Element.prototype.getTooltip = function() {
  'use strict';

  return this.tooltip_;
};


/**
 * This method sets a tool tip string to be shown when the user hovers over element element.
 * @param {string} tooltip the tool tip to be shown when hovering over this element
 */
H.ui.base.Element.prototype.setTooltip = function(tooltip) {
  'use strict';

  this.tooltip_ = tooltip;
  if (this.getElement()) {
    if (tooltip) {
      this.getElement().setAttribute('title', tooltip);
    } else {
      this.getElement().removeAttribute('title');
    }
  }
};


/**
 * This method returns the HTML element this UI element renders.
 *
 * Note: Before the UI element was rendered the method returns {@code null}.
 * @return {?HTMLElement} the element
 * @export
 * @publish
 */
H.ui.base.Element.prototype.getElement = function() {
  'use strict';

  return this['el'];
};


/**
 * @param {Element} el the HTML element representing this control
 * @private
 */
H.ui.base.Element.prototype.setElement_ = function(el) {
  'use strict';

  this['el'] = el;
};


/**
 * The current visibility of this element
 * @private
 * @type {boolean}
 */
H.ui.base.Element.prototype.visibility_ = true;


/**
 * Sets the visibility of this element.
 * @publish
 * @export
 * @param {boolean} visibility
 */
H.ui.base.Element.prototype.setVisibility = function(visibility) {
  'use strict';
  var el;
  this.visibility_ = visibility;
  if (el = this['el']) {
    el.style.display = visibility ? '' : 'none';
  }
};


/**
 * Returns the visibility of this element.
 * @publish
 * @export
 * @return {boolean} visibility
 */
H.ui.base.Element.prototype.getVisibility = function() {
  'use strict';
  return this.visibility_;
};


/**
 * This method adds a CSS class to this UI element (if it is not already present).
 * @param {string} className the CSS class name to add
 * @return {H.ui.base.Element} this UI element instance
 * @export
 * @publish
 */
H.ui.base.Element.prototype.addClass = function(className) {
  'use strict';

  var el = this.getElement();
  if (this.classNames_.indexOf(className) < 0) {
    this.classNames_.push(className);
  }

  if (el) {
    el.className = this.classNames_.join(' ');
  }
  return this;
};


/**
 * This method removes a CSS class from this UI element (if it is present).
 * @param {string} className the CSS class name to add
 * @return {H.ui.base.Element} this UI element instance
 * @export
 * @publish
 */
H.ui.base.Element.prototype.removeClass = function(className) {
  'use strict';

  var index = this.classNames_.indexOf(className),
      el = this.getElement();
  if (index > -1) {
    this.classNames_.splice(index, 1);
  }
  if (el) {
    el.className = this.classNames_.join(' ');
  }
  return this;
};


/**
 * Removes the UI element from the UI.
 */
H.ui.base.Element.prototype.remove = function() {
  'use strict';

  var el = this.getElement();
  if (el && el.parentNode) {
    el.parentNode.removeChild(el);
  }
};


/**
 * @override
 */
H.ui.base.Element.prototype.disposeInternal = function() {
  'use strict';

  /** @type {goog.events.EventTarget} */(H.util.getSuperProto(H.ui.base.Element)).disposeInternal.call(this);

  this.remove();
  this['el'] = null;
  this.data_ = null;
};


/**
 * This method disposes of the element.
 * @method H.ui.base.Element#dispose
 * @publish
 * @export
 */
H.ui.base.Element.prototype.dispose = function() {
  goog.events.EventTarget.prototype.dispose.call(this);
};


/**
 * This method allows to listen for specific event triggered by the object
 * @param {string} type name of event
 * @param {!Function} handler event handler function
 * @param {boolean=} opt_capture if set to true will listen in the capture phase (bubble otherwise)
 * @param {Object=} opt_scope scope for the handler function
 * @method H.ui.base.Element#addEventListener
 * @publish
 */

/**
 * This method will removed previously added listener from the event target
 * @param {string} type name of event
 * @param {!Function} handler previously added event handler
 * @param {boolean=} opt_capture if set to true will listen in the capture phase (bubble otherwise)
 * @param {Object=} opt_scope scope for the handler function
 * @method H.ui.base.Element#removeEventListener
 * @publish
 */
