goog.provide('H.ui.Pano');

goog.require('H.ui.Control');
goog.require('H.ui.LayoutAlignment');
goog.require('H.ui.base.PushButton');
goog.require('H.ui.icons.pano');

goog.require('H.ui.panorama.Discovery');



/**
 * This class represents the UI controls for panorama
 * @publish
 * @export
 *
 * @constructor
 * @param {H.ui.Pano.Options=} opt_options optional parameters to be passed to the map.
 *
 * @extends {H.ui.Control}
 */
H.ui.Pano = function(opt_options) {
  'use strict';
  var button,
      states = this.setupStates_(),
      that = this,
      val;

  H.ui.Control.call(this);

  this.setDisabled(true);
  if (opt_options) {
    // process the mapTypes option:
    this.processMapTypes_(opt_options['mapTypes']);
    if (val = opt_options['alignment']) {
      this.setAlignment(val);
    }
  }

  /**
   * @private
   * @type {H.ui.base.PushButton}
   */
  this['coverageButton_'] = new H.ui.base.PushButton({
    'onStateChange': function() {
      var states = that.states_;
      that.setState(this.getState() === H.ui.base.Button.State.DOWN ? states.coverage : states.inactive);
    }
  });

  /**
   * @private
   * @type {H.ui.base.Button}
   */
  this.leaveButton_ = button = new H.ui.base.Button({
    'onStateChange': function(evt) {
      that.map.setEngineType(H.Map.EngineType.P2D);
      that.setState(that.prevState_);
    }
  });

  this.addChild(this['coverageButton_']);
  this.addChild(this.leaveButton_);

  this.state_ = this.prevState_ = states.inactive;

  // By default we hide the control if panorama module is not loaded:
  //this.setVisibility(!!H.map.render.panorama);

};
H.lang.inherits(H.ui.Pano, H.ui.Control);


/**
 * @private
 * @param {H.service.MapTypes} mapTypes
 */
H.ui.Pano.prototype.processMapTypes_ = function(mapTypes) {
  'use strict';
  var fallbackMapTypes,
      key,
      keys,
      i;
  if (mapTypes) {
    this.mapTypes_ = mapTypes;
    this.fallbackMapTypes_ = fallbackMapTypes = [];
    keys = ['normal', 'satellite', 'terrain'];
    // Try to get the normal, satellite and terrain map types:
    for (i = 0; i < keys.length; i++) {
      if (mapTypes[key = keys[i]]) {
        fallbackMapTypes.unshift(mapTypes[key]);
      }
    }
    // Add all map types other than normal, satellite and terrain:
    for (key in mapTypes) {
      if (keys.indexOf(key) < 0) {
        fallbackMapTypes.unshift(mapTypes[key]);
      }
    }
    this.setDisabled(!fallbackMapTypes.length);
  }
};


/**
 * This methods sets up states for panorama UI,
 * some of the states label and tooltip values
 * will be filled with setLocalization
 * @private
 * @return {Object}
 */
H.ui.Pano.prototype.setupStates_ = function() {
  'use strict';
  this.states_ = {
    inactive: {
      label: H.ui.icons.pano.inactive
    },
    coverage: {
      label: H.ui.icons.pano.coverage
    },
    panorama: {}
  };
  return this.states_;
};


/**
 * @private
 * @type {Object.<string,string>}
 */
H.ui.Pano.prototype.state_;


/**
 * @private
 * @type {Object.<string,string>}
 */
H.ui.Pano.prototype.prevState_;


/**
 * @private
 * @type {H.map.layer.Layer}
 */
H.ui.Pano.prototype.prevBaseLayer_;


/**
 * The map types to use when looking for a fallback map layer (panorama <--> map) in descending order
 * @private
 * @type {Array.<H.service.MapType>}
 */
H.ui.Pano.prototype.fallbackMapTypes_;


/**
 * @private
 * @type {H.service.MapTypes}
 */
H.ui.Pano.prototype.mapTypes_;


/**
 * To get the map type group of the given layer
 * @private
 * @param {H.map.layer.Layer} layer
 * @return {H.service.MapType|undefined}
 */
H.ui.Pano.prototype.getMapTypeGroup_ = function(layer) {
  'use strict';
  var mapTypes = this.mapTypes_,
      key,
      group,
      found;
  outer:
  for (key in mapTypes) {
    group = mapTypes[key];
    for (key in group) {
      if (group[key] === layer) {
        found = true;
        break outer;
      }
    }
  }
  return found ? group : undefined;
};


/**
 * Returns the best matching base layer for the given type key. It tries to get the layer from the same map type group.
 * If no success it tries to find such layer in the fallbackMapTypes_ collection
 * @private
 * @param {H.map.layer.Layer} layer
 * @param {string} key the key of the base layer to search for, e.g. "panorama" or "map"
 * @return {H.map.layer.Layer|undefined} the layer which was found or undefined if no success
 */
H.ui.Pano.prototype.getBestMatchingLayer_ = function(layer, key) {
  'use strict';
  var group,
      bestMatch,
      fallbackMapTypes,
      i;
  if (layer && (group = this.getMapTypeGroup_(layer))) {
    bestMatch = group[key];
  }
  if (!bestMatch) {
    fallbackMapTypes = this.fallbackMapTypes_;
    i = fallbackMapTypes.length;
    while (i--) {
      if (bestMatch = fallbackMapTypes[i][key]) {
        break;
      }
    }
  }
  return bestMatch;
};


/**
 * @override
 * @export
 */
H.ui.Pano.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.Control.prototype['renderInternal'].call(this, element, doc);

  // TODO Detect the real state instead of assuming "inactive"!
  this.setState(this.states_.inactive);
  this.discovery_ = new H.ui.panorama.Discovery(/** @type {H.ui.UI} */(this.getParentEventTarget()));

  this.states_.inactive.tooltip = this.getLocalization().translate('panorama.showCoverage');
  this.states_.coverage.tooltip = this.getLocalization().translate('panorama.hideCoverage');
  this.states_.panorama.tooltip = this.getLocalization().translate('panorama.leave');
  this.states_.panorama.label = '<span style="padding: 0 0.5em">' +
      this.getLocalization().translate('panorama.leave') + '<span>';
  this.leaveButton_.setLabel(this.states_.panorama.label);
  this.leaveButton_.setTooltip(this.states_.panorama.tooltip);

  var that = this;
  this.discovery_.addEventListener('enter', function(evt) {
    var pos = evt.position;
    that.discovery_.disable();
    that.map.setCenter(evt.position, true);
    setTimeout(function() {
      that['coverageButton_'].setVisibility(false);
      that.map.setEngineType(H.Map.EngineType.PANORAMA);
    }, 200);
  });

  var layers = this.map.getLayers();

  this.onLayersChange_ = goog.bind(this.onLayersChange_, this);

  layers.addEventListener('add', this.onLayersChange_);
  layers.addEventListener('remove', this.onLayersChange_, this);
  layers.addEventListener('set', this.onLayersChange_, this);
};


/**
 * Method is responsible for disabling discovery and panorama button if base map layer was changed
 * on the map.
 * @private
 * @param {H.util.Event} evt from layers collectino
 */
H.ui.Pano.prototype.onLayersChange_ = function(evt) {
  'use strict';
  if (evt['idx'] === 0) {
    if (this.map &&
        this.getBestMatchingLayer_(this.map.getBaseLayer(), 'panorama') === this.map.getBaseLayer()) {
      this.discovery_.disable();
      this['coverageButton_'].setState(H.ui.base.Button.State['UP']);
    }
  }
};


/**
 * Sets the state of the Panorama Control
 * @param {Object} state
 */
H.ui.Pano.prototype.setState = function(state) {
  'use strict';
  var currState = this.state_,
      states = this.states_,
      isPano = state === states.panorama,
      isInactive,
      coverageButton = this['coverageButton_'],
      btnState,
      baseLayer;
  if (currState === states.inactive) {
    // Save the current base layer:
    this.prevBaseLayer_ = this.map.getBaseLayer();
  }
  if (isPano) {
    // Save the current state:
    this.prevState_ = currState;
    // TODO Activate the panorama engine
  } else {
    if (isInactive = state === states.inactive) {
      btnState = 'UP';
      // Restore the previous base layer:
      baseLayer = this.prevBaseLayer_;

      this.discovery_ && this.discovery_.disable();
    } else {
      btnState = 'DOWN';
      baseLayer = this.getBestMatchingLayer_(this.map.getBaseLayer(), 'panorama');

      //this.getCoverageLayerFor_(this.prevBaseLayer_));

      this.discovery_ && this.discovery_.enable();
    }
    if (baseLayer) {
      this.map.setBaseLayer(baseLayer);
    }
    coverageButton.setLabel(state.label);
    coverageButton.setTooltip(state.tooltip);
    // Set the button state in case of setState was triggered programatically:
    coverageButton.setState(H.ui.base.Button.State[btnState]);
  }
  this.leaveButton_.setVisibility(isPano);
  coverageButton.setVisibility(!isPano);

  this.state_ = state;
};


/**
 * @override
 * @export
 */
H.ui.Pano.prototype.onMapEngineTypeChange = function(engineType) {
  'use strict';
  var states = this.states_;
  this.setState(
      engineType === H.Map.EngineType.PANORAMA ?
      states.panorama :
      this.prevState_);
};


/**
 * @publish
 * @property {H.ui.LayoutAlignment} alignment the preferred layout alignment for this control
 * @property {H.service.MapTypes} mapTypes The map types to use
 *
 * @typedef {{
 *   alignment: (H.ui.LayoutAlignment|undefined),
 *   mapeTypes: (H.service.MapTypes)
 * }}
 */
H.ui.Pano.Options;
