goog.provide('H.ui.i18n.Localization');

goog.require('H.ui.i18n');
goog.require('H.ui.i18n.strings_de_DE');
goog.require('H.ui.i18n.strings_en_US');
goog.require('H.ui.i18n.strings_es_ES');
goog.require('H.ui.i18n.strings_fi_FI');
goog.require('H.ui.i18n.strings_fr_FR');
goog.require('H.ui.i18n.strings_it_IT');
goog.require('H.ui.i18n.strings_nl_NL');
goog.require('H.ui.i18n.strings_pl_PL');
goog.require('H.ui.i18n.strings_pt_BR');
goog.require('H.ui.i18n.strings_pt_PT');
goog.require('H.ui.i18n.strings_ru_RU');
goog.require('H.ui.i18n.strings_tr_TR');
goog.require('H.ui.i18n.strings_zh_CN');



/**
 * This class is used for internationalization of UI components.
 * @constructor
 * @param {string} locale locale code i.e en-GB
 * @param {Object=} opt_translationMap optional translation map for this locale
 *  if not provided Locale will be initialized with default translations if available
 *
 * @publish
 * @export
 */
H.ui.i18n.Localization = function(locale, opt_translationMap) {
  'use strict';
  var map;

  if (!goog.isString(locale)) {
    throw new TypeError('string locale code required');
  }

  if (!goog.isObject(opt_translationMap)) {
    //not translation map defined load default locale if available
    if (H.ui.i18n.defaultLocales.indexOf(locale) === -1) {
      throw new Error('Default locale \'' + locale + '\' does nto exist.');
    }
    map = H.ui.i18n.defaultStrings[locale];
  } else {
    map = opt_translationMap;
    H.ui.i18n.validateMap(locale, map);
  }

  /**
   * @private
   * @dict
   */
  this.map_ = map || {};

  /**
   * @type {string}
   * @private
   */
  this.locale_ = locale;
};


/**
 * This method returns current locale code i.e 'en-US'
 * @return {string} locale code
 * @export
 * @publish
 */
H.ui.i18n.Localization.prototype.getLocale = function() {
  'use strict';
  return this.locale_;
};


/**
 * This method returns translation keys for current
 * locale. Keys from this set can be used to get translations
 * via {@code translate} method.
 * @return {Array.<string>}
 * @export
 * @publish
 */
H.ui.i18n.Localization.prototype.getKeys = function() {
  'use strict';
  return Object.keys(this.map_);
};


/**
 * This method returns a boolean value indicating whether this localization
 * object has a translation for the specified translation key.
 * @param {string} key a transaltion key
 * @return {boolean} {@code true} if the key exists, otherwise {@code false}.
 * @export
 * @publish
 */
H.ui.i18n.Localization.prototype.hasKey = function(key) {
  'use strict';
  return !!(this.map_ && this.map_[key]);
};


/**
 * This method returns translation for provided key. It throws exception
 * if translation is not available
 * @param {string} key a translation key
 * @return {string} a localized string corresponding to provided key
 * @export
 * @publish
 */
H.ui.i18n.Localization.prototype.translate = function(key) {
  'use strict';
  var translation = this.map_[key];
  if (!goog.isString(translation)) {
    throw new Error('Translation missing for key \'' + key + '\'.');
  }
  return translation;
};

