goog.provide('H.ui.ScaleBar');

goog.require('H.ui.Control');



/**
 * This class represents a UI element showing the current zoom scale.
 * @constructor
 * @param {H.ui.ScaleBar.Options=} opt_options optional parameters to be passed to this scale bar.
 *
 * @extends {H.ui.Control}
 * @export
 * @publish
 */
H.ui.ScaleBar = function(opt_options) {
  'use strict';

  var options = opt_options || {};

  H.ui.Control.call(this);
  this.addClass('H_scalebar');
  this.setAlignment(options['alignment'] || 'bottom-right');

  this.maxWidth = 150;
  this.update = H.lang.bind(this.update, this);

  /**
   * @type {Node}
   * @private
   */
  this.svgEl_ = null;
  /**
   * @type {Node}
   * @private
   */
  this.scaleEl_ = null;
  /**
   * @type {Node}
   * @private
   */
  this.scaleShadowEl_ = null;
  /**
   * @type {Node}
   * @private
   */
  this.labelEl_ = null;
  /**
   * @type {Node}
   * @private
   */
  this.labelShadowEl_ = null;
};
H.lang.inherits(H.ui.ScaleBar, H.ui.Control);


/**
 * @override
 * @export
 */
H.ui.ScaleBar.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.Control.prototype['renderInternal'].call(this, element, doc);

  var childNodes;

  this.map.addEventListener('mapviewchangeend', this.update);

  element.innerHTML = this.getTemplate_();

  this.svgEl_ = element.firstChild;
  childNodes = this.svgEl_.childNodes;
  this.scaleEl_ = childNodes[0];
  this.scaleShadowEl_ = childNodes[1];
  this.labelEl_ = childNodes[2];
  this.labelShadowEl_ = childNodes[3];

  /**
   * @type {H.ui.UnitSystem}
   * @private
   */
  this.unitSystem_ = H.ui.UnitSystem['METRIC'];

  this.dispatchToggleUnitSystem_ = H.lang.bind(function(evt) {
    if (!this.isDisabled()) {
      this.dispatchEvent('toggleunitsystem');
      evt.preventDefault();
    }
  }, this);
  this.onStart_ = H.lang.bind(function(evt) {
    evt.preventDefault();
  }, this);

  H.ui.dom.on(element, 'start', this.onStart_);
  H.ui.dom.on(element, 'end', this.dispatchToggleUnitSystem_);

  this.setTooltip(this.getLocalization().translate('scale.switchToImperial'));

  this.update();
};


/**
 * Returns an svg markup for this control
 * @private
 * @return {string}
 */
H.ui.ScaleBar.prototype.getTemplate_ = function() {
  // Is inserted inside attribute, that's why \'..\'
  var font = 'font: bold 10px \'Lucida Grande\',\'Lucida Sans Unicode\',Arial,Helvetica,sans-serif';

  // All required styling should be inlined,
  // otherwise capturing of ui will not work.
  return [
    '<svg height="28" version="1.1" class="H_scalebar_svg" xmlns="http://www.w3.org/2000/svg">',
    '<polyline',
    ' style="fill:none;stroke:white;stroke-width:4;stroke-opacity:0.8"',
    ' points="2,19 2,26 98,26 98,19"/>',
    '<polyline',
    ' style="fill:none; stroke:black; stroke-width:2"',
    ' points="2,20 2,26 98,26 98,20"/>',
    '<text',
    ' style="stroke:white;stroke-width:2;stroke-opacity:0.8;text-anchor:end;', font, '"',
    ' x="90" y="22"></text>',
    '<text style="text-anchor:end;', font, ';color:black;" x="90" y="22"></text>',
    '</svg>'
  ].join('');
};


/**
 * Captures current ui state of the scale bar,
 * and call callback with its HTMLCanvasElement representation
 * @export
 * @param {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays,
 *                            default is {@code 1}
 * @param {function(HTMLCanvasElement=)} callback Callback function to call once result of
 *                                                the capturing is ready
 * @param {function(string)=} opt_errback Callback function to call if error occurerd during capturing
 */
H.ui.ScaleBar.prototype.capture = function(pixelRatio, callback, opt_errback) {
  var canvas =  /** @type {HTMLCanvasElement} */ (document.createElement('canvas')),
      ctx = canvas.getContext('2d'),
      scaleBarClone = H.util.cloneNode(this.svgEl_, document, false),
      dataUrl,
      tmpNode,
      width = parseFloat(scaleBarClone.getAttribute('width')),
      height = parseFloat(scaleBarClone.getAttribute('height')),
      scaledWidth = width * pixelRatio,
      scaledHeight = height * pixelRatio;

  // Scale SVG according to pixel ratio
  scaleBarClone.setAttribute('viewBox', '0 0 ' + width + ' ' + height);
  scaleBarClone.setAttribute('width', scaledWidth);
  scaleBarClone.setAttribute('height', scaledHeight);

  // Bug fix for FF when outerHTML of an SVG node is not available
  tmpNode = document.createElement('div');
  tmpNode.appendChild(scaleBarClone);

  // Convert SVG markup into data URL
  dataUrl = H.util.svgToDataUrl(tmpNode.innerHTML);

  canvas.width = scaledWidth;
  canvas.height = scaledHeight;

  // Load and draw an image to the canvas
  H.net.images.load(dataUrl, function(img) {
    ctx.drawImage(img, 0, 0);
    callback(canvas);
  }, opt_errback);
};


/**
 * @override
 * @export
 */
H.ui.ScaleBar.prototype.onUnitSystemChange = function(unitSystem) {
  'use strict';
  this.unitSystem_ = unitSystem;
  this.setTooltip(unitSystem === 'imperial' ?
      this.getLocalization().translate('scale.switchToMetric') :
          this.getLocalization().translate('scale.switchToImperial'));
  this.update();
};


/**
 * @override
 * @export
 */
H.ui.ScaleBar.prototype.onMapEngineTypeChange = function(engineType) {
  var hide = engineType !== H.Map.EngineType.P2D;
  this.getElement().style.display = hide ? 'none' : '';
};


/**
 * This method updates the scale bar's size and units.
 */
H.ui.ScaleBar.prototype.update = function() {
  'use strict';

  if (!this.getElement() || this.map && this.map.getEngine().type !== H.Map.EngineType.P2D) {
    return;
  }

  var delta = this.getDelta_(100),
      rawDistance,
      simplifiedDistance,
      unit,
      elementWidth,
      maxWidth = this.maxWidth;
  if (this.unitSystem_ === H.ui.UnitSystem['IMPERIAL']) {
    if (delta < H.ui.ScaleBar.MILE_IN_METERS_) {
      rawDistance = delta / H.ui.ScaleBar.FOOT_IN_METERS_;
      unit = this.getLocalization().translate('scale.ft');
    } else {
      rawDistance = delta / H.ui.ScaleBar.MILE_IN_METERS_;
      unit = this.getLocalization().translate('scale.mi');
    }
  }
  else {
    if (delta < 1000) {
      rawDistance = delta;
      unit = this.getLocalization().translate('scale.m');
    } else {
      rawDistance = delta / 1000;
      unit = this.getLocalization().translate('scale.km');
    }
  }

  simplifiedDistance = Math.pow(10, Math.floor(Math.log(rawDistance) / Math.LN10));

  elementWidth = 100 * (simplifiedDistance / rawDistance);

  if (elementWidth < (maxWidth / 5)) {
    simplifiedDistance *= 5;
    elementWidth *= 5;
  } else if (elementWidth < (maxWidth / 2)) {
    simplifiedDistance *= 2;
    elementWidth *= 2;
  } else if (elementWidth > maxWidth) {
    simplifiedDistance /= 2;
    elementWidth /= 2;
  }

  elementWidth = Math.round(elementWidth);

  this.svgEl_.setAttribute('width', elementWidth);

  // Change labels text
  this.labelShadowEl_.textContent = this.labelEl_.textContent = simplifiedDistance + ' ' + unit;
  // Change labels anchor,
  // 8px label offset subtracted
  this.labelShadowEl_.setAttribute('x', elementWidth - 8);
  this.labelEl_.setAttribute('x', elementWidth - 8);
  // Update our svg line width, by updating 'x' coords of two rightmost points
  // 2px stands for stroke width
  this.scaleEl_['points'].getItem(2).x = this.scaleShadowEl_['points'].getItem(2).x =
      this.scaleEl_['points'].getItem(3).x = this.scaleShadowEl_['points'].getItem(3).x =
          elementWidth - 2;
};


/**
 * @type {number}
 * @private
 */
H.ui.ScaleBar.MILE_IN_METERS_ = 1609.344;


/**
 * @type {number}
 * @private
 */
H.ui.ScaleBar.FOOT_IN_METERS_ = 0.3048006;


/**
 * This method returns the horizontal distance in meters which correspond to the pixelDistance on the map.
 * @param {number} pixelDistance the pixel distance to measure in meters
 * @return {number} the number of meters spanned by the pixel distance
 * @private
 */
H.ui.ScaleBar.prototype.getDelta_ = function(pixelDistance) {
  'use strict';

  var delta = 0,
      map = this.map,
      halfWidth,
      halfHeight,
      left,
      geoPoint,
      right;
  if (map) {
    halfWidth = Math.round(0.5 * map.getViewPort()['width']);
    halfHeight = Math.round(0.5 * map.getViewPort()['height']);
    geoPoint = map.screenToGeo(halfWidth, halfHeight);
    if (geoPoint) {
      left = H.geo.Point.fromIPoint(geoPoint);
      right = H.geo.Point.fromIPoint(map.screenToGeo(halfWidth + 1, halfHeight));

      delta = pixelDistance * left.distance(right);
    }
  }
  return delta;
};


/**
 * @typedef {{
 *   alignment: (string|undefined)
 * }}
 * @property {string=} alignment the preferred layout alignment for this control, default is {@code "bottom-right"}
 * @publish
 */
H.ui.ScaleBar.Options;


/**
 * This event is fired when unit system is changed
 * @event H.ui.ScaleBar#toggleunitsystem
 * @type {H.util.Event}
 * @publish
 */
