goog.provide('H.ui.base.Button');

goog.require('H.ui.base.Element');



/**
 * This class represents a button.
 *
 * @constructor
 * @param {H.ui.base.Button.Options=} opt_options optional parameters to be passed to the button instance
 * @extends {H.ui.base.Element}
 * @export
 * @publish
 */
H.ui.base.Button = function(opt_options) {
  'use strict';
  var options = opt_options || {};

  H.ui.base.Element.call(this, 'div', 'H_btn');

  this.setLabel(options['label'] || ' ');
  this.setTooltip(options['tooltip'] || '');
  this.setData(options['data']);
  this.setDisabled(!!options['disabled']);

  this.setState(H.ui.base.Button.State['UP'], true);

  if (options['onStateChange']) {
    this.addEventListener(H.ui.base.Element.EventType.STATECHANGE, options['onStateChange']);
  }

  this.onDown_ = H.lang.bind(this.onDown_, this);
  this.onUp_ = H.lang.bind(this.onUp_, this);
};
H.lang.inherits(H.ui.base.Button, H.ui.base.Element);


/**
 * @override
 * @export
 */
H.ui.base.Button.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.dom.on(element, 'start', this.onDown_);
  H.ui.dom.on(element, 'end', this.onUp_);
  H.ui.dom.on(element, 'out', this.onUp_);

  this.setLabel(this.getLabel(), true);
};


/**
 * This method returns the current state of the button.
 * @return {H.ui.base.Button.State} the current state of the button
 * @export
 * @publish
 */
H.ui.base.Button.prototype.getState = function() {
  'use strict';

  return this.state_;
};


/**
 * This method sets the state of this button.
 * @param {H.ui.base.Button.State} state the state to which this button is to be set
 * @param {boolean=} opt_suppressEvent an optional flag indicating that the onStateChange callback is
 *  not to be invoked
 * @return {H.ui.base.Button} this button instance
 * @export
 * @publish
 */
H.ui.base.Button.prototype.setState = function(state, opt_suppressEvent) {
  'use strict';

  if (state !== this.state_) {
    this.state_ = state;

    if (state === H.ui.base.Button.State['DOWN']) {
      this.addClass('H_active');
    } else {
      this.removeClass('H_active');
    }

    if (!opt_suppressEvent) {
      this.dispatchEvent(H.ui.base.Element.EventType.STATECHANGE);
    }
  }

  return this;
};


/**
 * This method returns the label string of this button.
 * @return {string} the label string of this button
 * @export
 * @publish
 */
H.ui.base.Button.prototype.getLabel = function() {
  'use strict';

  return this.label_;
};


/**
 * This method sets the label string of this button.
 * @param {string} label the label to set on this button
 * @param {boolean=} opt_force a boolean flag indicating that the new label should be set and propagated even if
 *  it has the same value as the current one.
 * @return {H.ui.base.Button} this button instance
 * @export
 * @publish
 */
H.ui.base.Button.prototype.setLabel = function(label, opt_force) {
  'use strict';

  if (opt_force || label !== this.label_) {
    this.label_ = label;

    if (this.getElement()) {
      this.getElement().innerHTML = label;
    }
  }
  return this;
};


/**
 * This button's onDown handler (setting the state to DOWN).
 * @param {Event} evt
 * @private
 */
H.ui.base.Button.prototype.onDown_ = function(evt) {
  'use strict';

  if (!this.isDisabled()) {
    this.setState(H.ui.base.Button.State['DOWN']);
  }
  evt.preventDefault();
};


/**
 * This button's onUp handler (setting the state to UP).
 * @param {Event} evt
 * @private
 */
H.ui.base.Button.prototype.onUp_ = function(evt) {
  'use strict';

  if (!this.isDisabled()) {
    this.setState(H.ui.base.Button.State['UP']);
  }
  evt.preventDefault();
};


/**
 * @override
 */
H.ui.base.Button.prototype.disposeInternal = function() {
  'use strict';

  var element = this.getElement();
  if (element) {
    H.ui.dom.off(element, 'start', this.onDown_);
    H.ui.dom.off(element, 'end', this.onUp_);
    H.ui.dom.off(element, 'out', this.onUp_);
  }
  H.ui.base.Element.prototype.disposeInternal.call(this);
};


/**
 * @typedef {{
 *   disabled: (boolean|undefined),
 *   label: (string|undefined),
 *   data: *,
 *   onStateChange: (function(H.util.Event)|undefined)
 * }}
 * @property {boolean=} disabled a flag to indicate whether this button is disabled, default is {@code false}
 * @property {string=} label the mark up (used as innerHTML) to be rendered into this button
 * @property {function(H.util.Event)=} onStateChange an optional event listener for the {@code onstatechange} event
 *  to be immediately added
 * @property {*} data an arbitrary data object to be stored with this button
 * @publish
 */
H.ui.base.Button.Options;


/**
 * This enumeration represents the possible state a button can have, namely 'UP' and 'DOWN'
 * @enum {string}
 * @export
 * @publish
 */
H.ui.base.Button.State = {
  /**
   * The button's state when it is not pressed. ('up')
   */
  'UP': 'up',
  /**
   * The button's state when it is pressed. ('down')
   */
  'DOWN': 'down'
};


/**
 * This event is dispatched by the button when it's state changes.
 * @event H.ui.base.Button#statechange
 * @type {H.util.Event}
 * @publish
 */
