goog.provide('H.ui.MapSettingsControl');


goog.require('H.ui.Control');
goog.require('H.ui.base.OverlayPanel');
goog.require('H.ui.base.PushButton');
goog.require('H.ui.base.RadioGroup');
goog.require('H.ui.icons.mapSettings');
goog.require('H.ui.traffic.TrafficIncidents');



/**
 * This class represents a menu control allowing to control which map type the map shows, etc.
 *
 * @constructor
 * @param {H.ui.MapSettingsControl.Options=} opt_options optional parameters to be passed to this control
 *
 * @extends {H.ui.Control}
 *
 * @export
 * @publish
 */
H.ui.MapSettingsControl = function(opt_options) {
  'use strict';

  var options = opt_options || {};

  H.ui.Control.call(this);

  this.onTrafficStateChange_ = H.lang.bind(this.onTrafficStateChange_, this);
  this.onTransitStateChange_ = H.lang.bind(this.onTransitStateChange_, this);
  this.onLayerSelect_ = H.lang.bind(this.onLayerSelect_, this);
  //this.onBaseLayerChange_ = H.lang.bind(this.onBaseLayerChange_, this);
  this.onLayersChange_ = H.lang.bind(this.onLayersChange_, this);
  this.closeMenu = H.lang.bind(this.closeMenu, this);

  /**
   * @type {H.ui.base.OverlayPanel}
   * @private
   */
  this.menu_ = new H.ui.base.OverlayPanel();
  /**
   * @type {H.ui.base.RadioGroup}
   * @private
   */
  this.mapTypes_ = new H.ui.base.RadioGroup({
    'onActiveButtonChange': this.onLayerSelect_
  });

  /**
   * @type {H.ui.base.PushButton}
   * @private
   */
  this.transitBtn_ = new H.ui.base.PushButton({
    'onStateChange': this.onTransitStateChange_
  });
  /**
   * @type {H.ui.base.PushButton}
   * @private
   */
  this.trafficBtn_ = new H.ui.base.PushButton({
    'onStateChange': this.onTrafficStateChange_
  });

  this.trafficIncidents_ = new H.ui.traffic.TrafficIncidents();
  this.trafficIncidents_.setIncidentsLayer(options['incidents']);

  /**
   * @type {H.ui.base.PushButton}
   * @private
   */
  this.menuButton_ = new H.ui.base.PushButton({
    'label': H.ui.icons.mapSettings,
    'onStateChange': H.lang.bind(function(evt) {
      if (evt.target.getState() === H.ui.base.Button.State['DOWN']) {
        this.menu_.setState(H.ui.base.OverlayPanel.State['OPEN']);
        this.menu_.pointToControl(this);
      } else {
        this.menu_.setState(H.ui.base.OverlayPanel.State['CLOSED']);
      }
    }, this)
  });

  this.menu_.addChild(this.mapTypes_);
  this.menu_.addChild(this.trafficBtn_);
  this.menu_.addChild(this.transitBtn_);
  this.menu_.addChild(this.trafficIncidents_.getButton());

  this.addChild(this.menuButton_);
  this.addChild(this.menu_);

  //apply options or defaults
  if (options['entries']) {
    options['entries'].forEach(function(entry) {
      this.addMapTypeEntry(entry);
    }, this);
  }
  this.setAlignment(options['alignment'] || H.ui.LayoutAlignment.BOTTOM_RIGHT);
};
H.lang.inherits(H.ui.MapSettingsControl, H.ui.Control);


/**
 * @override
 * @export
 */
H.ui.MapSettingsControl.prototype.renderInternal = function(element, doc) {
  'use strict';

  var layers = this.getMap().getLayers();

  H.ui.Control.prototype['renderInternal'].call(this, element, doc);

  this.mapTypes_.setTitle(this.getLocalization().translate('layers.choose'));
  this.mapTypes_.getButtons().forEach(function(button) {
    var label = button.getLabel();
    if (label.substr(0, 7) === 'layers.') {
      button.setLabel(this.getLocalization().translate(label));
    }
  }, this);
  this.transitBtn_.setLabel(this.getLocalization().translate('layer.transit'));
  this.trafficBtn_.setLabel(this.getLocalization().translate('layer.traffic'));
  this.menuButton_.setTooltip(this.getLocalization().translate('layers.choose'));

  this.trafficIncidents_.setUi(/** @type {H.ui.UI} */(this.getParentEventTarget()));

  //this.getMap().addEventListener('baselayerchange', this.onBaseLayerChange_);
  layers.addEventListener('add', this.onLayersChange_);
  layers.addEventListener('remove', this.onLayersChange_);
  layers.addEventListener('set', this.onLayersChange_);

  this.getMap().addEventListener('tap', this.closeMenu);
  this.onBaseLayerChange_();
};


/**
 * Handler when the layer drop down selection changes.
 * @param {H.util.Event=} opt_evt
 * @private
 */
H.ui.MapSettingsControl.prototype.onLayerSelect_ = function(opt_evt) {
  'use strict';

  var mapTypeButton = this.mapTypes_.getActiveButton(),
      mapType = mapTypeButton ? mapTypeButton.getData() : null,
      baseLayerToUse;

  if (mapType) {
    // OK, now here is where it becomes a bit complicated
    // Here we choose which layer to activate:

    // By default we assume we are using the 'map' layer
    baseLayerToUse = mapType['map'];

    // Check it this map type has a 'transit' layer and if so enable the button for it
    // Then if the 'transit' layer is available AND the button is pressed, we will assume to
    // use that layer as the base layer
    this.transitBtn_.setDisabled(!mapType['transit']);
    if (mapType['transit']) {
      if (!this.transitBtn_.isDisabled() && this.transitBtn_.getState() === H.ui.base.Button.State['DOWN']) {
        baseLayerToUse = mapType['transit'];
      }
    }

    // No do the same for traffic, i.e. check if it is available, enable the button and if the button's
    // state us down, use the traffic layer as a base layer

    this.trafficBtn_.setDisabled(!mapType['traffic']);
    if (mapType['traffic']) {
      if (!this.trafficBtn_.isDisabled() && this.trafficBtn_.getState() === H.ui.base.Button.State['DOWN']) {
        baseLayerToUse = mapType['traffic'];
      }
    }

    //if we have a base layer and a map, we can go ahead and set the base layer
    if (baseLayerToUse && this.map) {
      this.map.setBaseLayer(baseLayerToUse);
    }
  } else {
    //If we do not have a map type (no button is active), we disable the traffic buttons
    this.transitBtn_.setDisabled(true);
    this.trafficBtn_.setDisabled(true);
  }
};


/**
 * Handler for state changes of the traffic button
 * @param {H.util.Event} evt the current state of the button
 * @private
 */
H.ui.MapSettingsControl.prototype.onTrafficStateChange_ = function(evt) {
  'use strict';

  if (this.trafficBtn_.getState() === H.ui.base.Button.State['DOWN']) {
    //when traffic is activated we deactivate PT
    this.transitBtn_.setState(H.ui.base.Button.State['UP'], true);
  }
  this.onLayerSelect_();
};


/**
 * Handler for state changes of the traffic button
 * @param {H.util.Event} evt the event signaling the state change
 * @private
 */
H.ui.MapSettingsControl.prototype.onTransitStateChange_ = function(evt) {
  'use strict';

  //when PT is activated we deactivate traffic
  if (this.transitBtn_.getState() === H.ui.base.Button.State['DOWN']) {
    this.trafficBtn_.setState(H.ui.base.Button.State['UP'], true);
  }
  this.onLayerSelect_();
};


/**
 * @private
 * @param {H.util.OList.Event} evt the change event from the data model
 */
H.ui.MapSettingsControl.prototype.onLayersChange_ = function(evt) {
  if (evt['idx'] === 0) {
    this.onBaseLayerChange_(evt);
  } else {
    this.trafficIncidents_.updateButtonState();
  }
};


/**
 * Handles base layer changes on the map (possibly from the outside world)
 * @param {H.util.ChangeEvent=} opt_evt
 * @private
 */
H.ui.MapSettingsControl.prototype.onBaseLayerChange_ = function(opt_evt) {
  'use strict';

  var found = null,
      baseLayer = opt_evt ? opt_evt['added'] : this.map.getBaseLayer();

  this.mapTypes_.getButtons().forEach(function(button) {
    var data = button.getData();
    if (data['map'] === baseLayer) {
      this.mapTypes_.setActiveButton(button, true);
      this.transitBtn_.setState(H.ui.base.Button.State['UP'], true);
      this.trafficBtn_.setState(H.ui.base.Button.State['UP'], true);
      found = data;
    } else if (data['transit'] && data['transit'] === baseLayer) {
      this.mapTypes_.setActiveButton(button, true);
      this.transitBtn_.setState(H.ui.base.Button.State['DOWN'], true);
      this.trafficBtn_.setState(H.ui.base.Button.State['UP'], true);
      found = data;
    } else if (data['traffic'] && data['traffic'] === baseLayer) {
      this.mapTypes_.setActiveButton(button, true);
      this.transitBtn_.setState(H.ui.base.Button.State['UP'], true);
      this.trafficBtn_.setState(H.ui.base.Button.State['DOWN'], true);
      found = data;
    }
  }, this);

  if (!found) {
    this.mapTypes_.setActiveButton(null, true);
  } else {
    this.transitBtn_.setDisabled(!found['transit']);
    this.trafficBtn_.setDisabled(!found['traffic']);
  }
};


/**
 * @override
 * @export
 */
H.ui.MapSettingsControl.prototype.onMapEngineTypeChange = function(engineType) {
  'use strict';

  var hide = engineType !== H.Map.EngineType.P2D;
  this.getElement().style.display = hide ? 'none' : '';
};


/**
 * This method closes the map settings panel.
 */
H.ui.MapSettingsControl.prototype.closeMenu = function() {
  'use strict';

  this.menuButton_.setState(H.ui.base.Button.State['UP']);
};


/**
 * This method add a map type entry to this map settings control.
 * @param {H.ui.MapSettingsControl.MapTypeEntry} entry
 */
H.ui.MapSettingsControl.prototype.addMapTypeEntry = function(entry) {
  'use strict';

  this.mapTypes_.addButton(new H.ui.base.PushButton({
    'label': entry['name'],
    'data': entry['mapType']
  }));
};


/**
 * This method removes a map type entry from this map settings control.
 * @param {H.ui.MapSettingsControl.MapTypeEntry} entry
 */
H.ui.MapSettingsControl.prototype.removeMapTypeEntry = function(entry) {
  'use strict';

  var buttons = this.mapTypes_.getButtons(),
      i = buttons.length,
      button;
  while (i--) {
    if (buttons[i].getData() === entry['mapType']) {
      button = buttons[i];
    }
  }

  if (button) {
    this.mapTypes_.removeButton(button);
  }
};


/**
 * Sets the traffic incidents layer to be shown when activating the respective button
 * on the map settings control.
 *
 * Setting this layer to a {@code falsy} value will hide the button.
 *
 * @param {H.map.layer.Layer} incidentsLayer the incidents layer
 * @export
 * @publish
 */
H.ui.MapSettingsControl.prototype.setIncidentsLayer = function(incidentsLayer) {
  this.trafficIncidents_.setIncidentsLayer(incidentsLayer);
};


/**
 * @typedef {{
 *   alignment: (string|undefined),
 *   entries: (Array.<H.ui.MapSettingsControl.MapTypeEntry>|undefined),
 *   incidents: (H.map.layer.Layer|undefined)
 * }}
 * @property {string=} alignment the layout alignment which should be applied to this control,
 *           default is {@link H.ui.LayoutPositon.BOTTOM_RIGHT}
 * @property {Array.<H.ui.MapSettingsControl.MapTypeEntry>=} entries the map type entries to be shown in this
 *   map settings control
 * @property {H.map.layer.Layer} incidents the traffic incidents layer to be activated by the map settings
 *   control
 * @publish
 */
H.ui.MapSettingsControl.Options;


/**
 * The map type entry is an object containing a display name and a map type object to which it refers.
 * @typedef {{
 *   name: (string),
 *   mapType: (H.service.MapType)
 * }}
 * @property {string} name label which describes the map type
 * @property {H.service.MapType} mapType reference to map type
 * @publish
 */
H.ui.MapSettingsControl.MapTypeEntry;
