goog.provide('H.ui.base.RadioGroup');

goog.require('H.ui.base.Container');



/**
 * This class represents a group of push buttons of which only one can be active at a time.
 *
 * @constructor
 * @param {H.ui.base.RadioGroup.Options=} opt_options optional parameters to be passed to the group
 * @extends {H.ui.base.Container}
 * @export
 * @publish
 */
H.ui.base.RadioGroup = function(opt_options) {
  'use strict';

  var options = opt_options || {};

  H.ui.base.Container.call(this, 'div', 'H_rdo');

  /**
   * @type {H.ui.base.Element}
   * @private
   */
  this.title_ = new H.ui.base.Element('div', 'H_rdo_title');
  /**
   * @type {H.ui.base.Container}
   * @private
   */
  this.buttons_ = new H.ui.base.Container('div', 'H_rdo_buttons');

  this.addChild(this.title_);
  this.addChild(this.buttons_);

  this.onButtonStateChange_ = H.lang.bind(function(evt) {
    if (evt.target.getState() === H.ui.base.Button.State['DOWN']) {
      this.setActiveButton(evt.target, true);
      this.dispatchEvent(H.ui.base.RadioGroup.EventType.ACTIVEBUTTONCHANGE);
    } else {
      evt.target.setState(H.ui.base.Button.State['DOWN'], true);
    }
  }, this);
  this.addEventListener(H.ui.base.Element.EventType.STATECHANGE, this.onButtonStateChange_);

  this.setTitle(options['title'] || '');

  if (options['onActiveButtonChange']) {
    this.addEventListener(H.ui.base.RadioGroup.EventType.ACTIVEBUTTONCHANGE, options['onActiveButtonChange']);
  }

  if (options['buttonsOptions']) {
    options['buttonsOptions'].forEach(function(options) {
      this.addButton(new H.ui.base.PushButton(options));
    }, this);
  }
};
H.lang.inherits(H.ui.base.RadioGroup, H.ui.base.Container);


/**
 * List of events fired by the radion group
 * @enum {string}
 */
H.ui.base.RadioGroup.EventType = {
  /**
   * This event is fired when a radio button becomes active.
   *
   * @event H.ui.base.RadioGroup#activebuttonchange
   * @type {H.util.Event}
   * @publish
   */
  ACTIVEBUTTONCHANGE: 'activebuttonchange'
};


/**
 * @override
 * @export
 */
H.ui.base.RadioGroup.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.base.Container.prototype['renderInternal'].call(this, element, doc);
  this.setTitle(this.getTitle(), true);
};


/**
 * This method set the title of this group.
 * @param {string} title the new title of this group
 * @param {boolean=} opt_force an optional flag indicating that the new value is to be updated and propagated even if
 *  it has the same value as the current title
 * @return {H.ui.base.RadioGroup} this group instance
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.setTitle = function(title, opt_force) {
  'use strict';

  if (title !== this.titleString_ || opt_force) {
    this.titleString_ = title;

    if (this.title_.getElement()) {
      this.title_.getElement().innerHTML = this.titleString_;
    }
  }
  return this;
};


/**
 * This method returns the current title string of this group.
 * @return {string} the current title
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.getTitle = function() {
  'use strict';

  return this.titleString_;
};


/**
 * This method adds an button element to this group.
 * @param {H.ui.base.PushButton} button the button to add
 * @return {H.ui.base.RadioGroup} this group instance
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.addButton = function(button) {
  'use strict';

  if (!(button instanceof H.ui.base.PushButton)) {
    throw new Error('PushButton expected');
  }

  button.setParentEventTarget(this);
  this.buttons_.addChild(button);
  return this;
};


/**
 * This method removes an button element from this group.
 * @param {H.ui.base.PushButton} button the button to remove
 * @return {H.ui.base.RadioGroup} this group instance
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.removeButton = function(button) {
  'use strict';

  this.buttons_.removeChild(button);
  button.setParentEventTarget(null);
  return this;
};


/**
 * This method returns the buttons currently registered with this group.
 * @return {Array.<H.ui.base.PushButton>} the buttons
 *
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.getButtons = function() {
  'use strict';

  return this.buttons_.getChildren();
};


/**
 * This method sets the active button of this radio group.
 * @param {H.ui.base.PushButton} button the button to be made the active one
 * @param {boolean=} opt_suppressEvent an optional boolean flag indicating that no statechange event is to be fired
 *
 * @export
 * @publish
 */
H.ui.base.RadioGroup.prototype.setActiveButton = function(button, opt_suppressEvent) {
  'use strict';

  var buttons = this.getButtons(),
      i = buttons.length;
  while (i--) {
    buttons[i].setState(
        buttons[i] === button ? H.ui.base.Button.State['DOWN'] : H.ui.base.Button.State['UP'],
        opt_suppressEvent);
  }
};


/**
 * This method returns the currently active button from this group.
 * @return {H.ui.base.PushButton} the currently active button
 */
H.ui.base.RadioGroup.prototype.getActiveButton = function() {
  'use strict';

  var buttons = this.getButtons(),
      i = buttons.length;
  while (i--) {
    if (buttons[i].getState() === H.ui.base.Button.State['DOWN']) {
      return buttons[i];
    }
  }
  return null;
};


/**
 * @override
 */
H.ui.base.RadioGroup.prototype.disposeInternal = function() {
  'use strict';

  this.removeEventListener(H.ui.base.Element.EventType.STATECHANGE, this.onButtonStateChange_);
  H.ui.base.Container.prototype.disposeInternal.call(this);
};


/**
 * @typedef {{
 *   title: (string|undefined),
 *   buttonsOptions: (Array.<H.ui.base.Button.Options>|undefined)
 * }}
 * @property {string=} title an optional title string for this group
 * @property {Array.<H.ui.base.Button.Options>=} buttons an optional set of button options to be added to this
 *  group
 * @publish
 */
H.ui.base.RadioGroup.Options;


/**
   * This event is fired when RadioGroup's state is modified.
   * @event H.ui.base.RadioGroup#statechange
   * @type {H.util.Event}
   * @publish
   */
