goog.provide('H.ui.base.Container');

goog.require('H.ui.base.Element');



/**
 * This class represents an abstract base class for container UI elements such as Lists and Panels.
 *
 * @extends {H.ui.base.Element}
 * @constructor
 * @param {string=} opt_elementType the type of HTML element this UI element renders as, default is {@code 'div'}
 * @param {string=} opt_className an optional class name to be used on this element
 * @param {Array.<H.ui.base.Element>=} opt_children optional child elements to be added to this container
 * @export
 * @publish
 */
H.ui.base.Container = function(opt_elementType, opt_className, opt_children) {
  'use strict';

  H.ui.base.Element.call(this, opt_elementType, opt_className);

  this.children_ = opt_children ? [].concat(opt_children) : [];
};
H.lang.inherits(H.ui.base.Container, H.ui.base.Element);


/**
 * @override
 * @export
 */
H.ui.base.Container.prototype.setDisabled = function(disabled, opt_force) {
  'use strict';

  var i = this.children_ ? this.children_.length : 0;
  H.ui.base.Element.prototype.setDisabled.call(this, disabled, opt_force);
  while (i--) {
    this.children_[i].setDisabled(disabled, opt_force);
  }

  return this;
};


/**
 * Adds a child element to be rendered within the container element.
 * @param {H.ui.base.Element} child the child element to be added
 * @return {H.ui.base.Container} this container instance
 * @export
 * @publish
 */
H.ui.base.Container.prototype.addChild = function(child) {
  'use strict';

  var el = this.getElement();

  this.children_.push(child);

  if (el) {
    child.render(el.ownerDocument);
    el.appendChild(child.getElement());
  }
  return this;
};


/**
 * Returns the child collection of this container.
 * @return {Array.<H.ui.base.Element>} the children of this container
 * @expose
 * @publish
 */
H.ui.base.Container.prototype.getChildren = function() {
  'use strict';

  return [].concat(this.children_);
};


/**
 * Removes a child element from this container's child collection.
 * @param {H.ui.base.Element} child the child element to be removed
 * @export
 * @publish
 */
H.ui.base.Container.prototype.removeChild = function(child) {
  'use strict';

  var index = this.children_.indexOf(child),
      el = this.getElement();
  if (index >= 0) {
    this.children_.splice(index, 1);
    if (el) {
      el.removeChild(child.getElement());
    }
  }
};


/**
 * @override
 * @export
 */
H.ui.base.Container.prototype.renderInternal = function(element, doc) {
  'use strict';

  var i = 0,
      l = this.children_.length;
  for (; i < l; i++) {
    element.appendChild(this.children_[i]['render'](doc));
  }
};


/**
 * @override
 */
H.ui.base.Container.prototype.disposeInternal = function() {
  'use strict';

  this.children_.forEach(this.disposeOfChild_, this);
  H.ui.base.Element.prototype.disposeInternal.call(this);
};


/**
 * Disposes of a single child element within this container
 * @param {H.ui.base.Element} child the child to be disposed of
 * @private
 */
H.ui.base.Container.prototype.disposeOfChild_ = function(child) {
  'use strict';

  child.dispose();
};

