goog.provide('H.ui.base.PushButton');

goog.require('H.ui.base.Button');



/**
 * This class represents a button, which will keep its state when pressed.
 * Pressing once will change this button's state to down. Pressing it again
 * will change the button's state to up.
 *
 * @constructor
 * @param {H.ui.base.Button.Options=} opt_options optional parameters to be passed to this button instance.
 * @extends {H.ui.base.Button}
 *
 * @export
 * @publish
 */
H.ui.base.PushButton = function(opt_options) {
  'use strict';

  H.ui.base.Button.call(this, opt_options);

  this.onEnd_ = function(evt) {
    evt.preventDefault();
  };

  /**
   * @type {function(Event)}
   * @private
   */
  this.onStart_ = H.lang.bind(function(evt) {
    if (!this.isDisabled()) {
      this['toggleState']();
    }
    evt.preventDefault();
  }, this);
};
H.lang.inherits(H.ui.base.PushButton, H.ui.base.Button);


/**
 * @override
 * @export
 */
H.ui.base.PushButton.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.dom.on(element, 'start', this.onStart_);
  H.ui.dom.on(element, 'end', this.onEnd_);
  this.setLabel(this.getLabel(), true);
};


/**
 * This method toggles this button's state between the two possible button states (see
 * {@link H.ui.base.Button.State}).
 *
 * @return {H.ui.base.PushButton} this button instance
 * @export
 * @publish
 */
H.ui.base.PushButton.prototype.toggleState = function() {
  'use strict';
  var DOWN = H.ui.base.Button.State['DOWN'];
  this.setState(this.getState() === DOWN ? H.ui.base.Button.State['UP'] : DOWN);
  return this;
};


/**
 * Disposes of this button
 */
H.ui.base.PushButton.prototype.disposeInternal = function() {
  'use strict';

  H.ui.dom.off(this.getElement(), 'start', this.onStart_);
  H.ui.base.Button.prototype.disposeInternal.call(this);
};
