goog.provide('H.ui.base.OverlayPanel');

goog.require('H.ui.base.Container');



/**
 * This class represents a panel that points to a control.
 *
 * @constructor
 * @extends {H.ui.base.Container}
 * @export
 * @publish
 */
H.ui.base.OverlayPanel = function() {
  'use strict';

  this.state_ = H.ui.base.OverlayPanel.State['CLOSED'];

  H.ui.base.Container.call(this, 'div', 'H_overlay');
};
H.lang.inherits(H.ui.base.OverlayPanel, H.ui.base.Container);


/**
 * This method sets the state of this panel.
 * @param {H.ui.base.OverlayPanel.State} state the state of this panel
 * @param {boolean=} opt_force a boolean value indicating that the value should be propagated even if it is the same as
 *  the current value
 * @return {H.ui.base.OverlayPanel} this panel instance
 * @export
 * @publish
 */
H.ui.base.OverlayPanel.prototype.setState = function(state, opt_force) {
  'use strict';

  if (state !== this.state_ || opt_force) {
    this.state_ = state;

    if (state === H.ui.base.OverlayPanel.State['OPEN']) {
      this.addClass('H_open');
    } else {
      this.removeClass('H_open');
    }
  }
  return this;
};


/**
 * This method returns the current state of the panel.
 * @return {H.ui.base.OverlayPanel.State}
 * @export
 * @publish
 */
H.ui.base.OverlayPanel.prototype.getState = function() {
  return this.state_;
};


/**
 * This method adjusts the alignment of the overlay panel to point to the provided control.
 * @param {H.ui.Control} control the control to which to point
 * @export
 * @publish
 */
H.ui.base.OverlayPanel.prototype.pointToControl = function(control) {
  'use strict';

  var layoutAlignment = control.getAlignment(),
      controlEl = control.getElement(),
      el = this.getElement(),
      style = el.style,
      verticalAlignment;


  // Reset all styles that are used for alignment of the overlay panel
  this.removeClass('H_top');
  this.removeClass('H_middle');
  this.removeClass('H_bottom');
  this.removeClass('H_left');
  this.removeClass('H_center');
  this.removeClass('H_right');
  style.bottom = style.top = style.left = style.right = style.margin = '';

  // now apply the correct styles for this control

  //first we check the vertical alignment
  if (/top/g.test(layoutAlignment)) {
    this.addClass('H_top');
    style.top = '0';
    style.margin = '0 1.2em';
    verticalAlignment = -1;
  } else if (/bottom/g.test(layoutAlignment)) {
    this.addClass('H_bottom');
    style.bottom = '0';
    style.margin = '0 1.2em';
    verticalAlignment = 1;
  } else {
    this.addClass('H_middle');
    style.top = '50%';
    style.margin = '0 1.2em';
    style.marginTop = (-Math.round(el.offsetHeight * 0.5)) + 'px';
    verticalAlignment = 0;
  }

  if (/left/g.test(layoutAlignment)) {
    this.addClass('H_left');
    style.left = controlEl.offsetWidth + 'px';
    style.marginLeft = '1.2em';
  } else if (/right/g.test(layoutAlignment)) {
    this.addClass('H_right');
    style.right = controlEl.offsetWidth + 'px';
    style.marginRight = '1.2em';
  } else {
    this.addClass('H_center');
    style.left = '50%';
    if (verticalAlignment < 0) {
      style.top = controlEl.offsetHeight + 'px';
      style.marginTop = '1.2em';
    } else if (verticalAlignment > 0) {
      style.bottom = controlEl.offsetHeight + 'px';
      style.marginBottom = '1.2em';
    }
    style.marginLeft = (-Math.round(el.offsetWidth * 0.5)) + 'px';

    controlEl.appendChild(el);
  }
};


/**
 * @enum {string}
 * @export
 * @publish
 */
H.ui.base.OverlayPanel.State = {
  /**
   * Defines a state where the OverlayPanel is open and visible
   */
  'OPEN': 'open',
  /**
   * Defines a state where the OverlayPanel is closed and invisible
   */
  'CLOSED': 'closed'
};
