goog.provide('H.ui.Layout');

goog.require('H.ui.LayoutAlignment');



/**
 * This class encapsulates the layout functionality for UI element alignments.
 * @constructor
 * @param {Element} uiEl the UI element to which this Layout belongs.
 * @param {Document} doc the document into which this layout renders
 */
H.ui.Layout = function(uiEl, doc) {
  'use strict';

  var anchors = {},
      corners = {},
      Alignment = H.ui.LayoutAlignment,
      Css = H.ui.Layout.CssClassNames,
      mkEl = H.ui.dom.mkEl,
      append = H.ui.dom.append;

  anchors[Alignment.TOP_LEFT] = mkEl(doc, 'div', [Css.ANCHOR, Css.HORIZONTAL].join(' '));
  anchors[Alignment.TOP_CENTER] = mkEl(doc, 'div', [Css.ANCHOR, Css.TOP, Css.CENTER, Css.HORIZONTAL].join(' '));
  anchors[Alignment.TOP_RIGHT] = mkEl(doc, 'div', [Css.ANCHOR, Css.HORIZONTAL].join(' '));
  anchors[Alignment.LEFT_TOP] = mkEl(doc, 'div', [Css.ANCHOR, Css.VERTICAL].join(' '));
  anchors[Alignment.LEFT_MIDDLE] = mkEl(doc, 'div', [Css.ANCHOR, Css.LEFT, Css.MIDDLE, Css.VERTICAL].join(' '));
  anchors[Alignment.LEFT_BOTTOM] = mkEl(doc, 'div', [Css.ANCHOR, Css.VERTICAL].join(' '));
  anchors[Alignment.RIGHT_TOP] = mkEl(doc, 'div', [Css.ANCHOR, Css.VERTICAL].join(' '));
  anchors[Alignment.RIGHT_MIDDLE] = mkEl(doc, 'div', [Css.ANCHOR, Css.RIGHT, Css.MIDDLE, Css.VERTICAL].join(' '));
  anchors[Alignment.RIGHT_BOTTOM] = mkEl(doc, 'div', [Css.ANCHOR, Css.VERTICAL].join(' '));
  anchors[Alignment.BOTTOM_LEFT] = mkEl(doc, 'div', [Css.ANCHOR, Css.HORIZONTAL].join(' '));
  anchors[Alignment.BOTTOM_CENTER] = mkEl(doc, 'div', [Css.ANCHOR, Css.BOTTOM, Css.CENTER, Css.HORIZONTAL].join(' '));
  anchors[Alignment.BOTTOM_RIGHT] = mkEl(doc, 'div', [Css.ANCHOR, Css.HORIZONTAL].join(' '));

  corners[Alignment.TOP_LEFT] = mkEl(doc, 'div', [Css.TOP, Css.LEFT].join(' '));
  corners[Alignment.TOP_RIGHT] = mkEl(doc, 'div', [Css.TOP, Css.RIGHT].join(' '));
  corners[Alignment.BOTTOM_LEFT] = mkEl(doc, 'div', [Css.BOTTOM, Css.LEFT].join(' '));
  corners[Alignment.BOTTOM_RIGHT] = mkEl(doc, 'div', [Css.BOTTOM, Css.RIGHT].join(' '));

  append(corners[Alignment.TOP_LEFT], anchors[Alignment.TOP_LEFT], anchors[Alignment.LEFT_TOP]);
  append(corners[Alignment.TOP_RIGHT], anchors[Alignment.TOP_RIGHT], anchors[Alignment.RIGHT_TOP]);
  append(corners[Alignment.BOTTOM_LEFT], anchors[Alignment.LEFT_BOTTOM], anchors[Alignment.BOTTOM_LEFT]);
  append(corners[Alignment.BOTTOM_RIGHT], anchors[Alignment.RIGHT_BOTTOM], anchors[Alignment.BOTTOM_RIGHT]);

  append(uiEl,
      corners[Alignment.TOP_LEFT],
      corners[Alignment.TOP_RIGHT],
      corners[Alignment.BOTTOM_LEFT],
      corners[Alignment.BOTTOM_RIGHT],
      anchors[Alignment.TOP_CENTER],
      anchors[Alignment.LEFT_MIDDLE],
      anchors[Alignment.RIGHT_MIDDLE],
      anchors[Alignment.BOTTOM_CENTER]);

  /**
   * This property holds the anchor elements to which UI elements can be appended.
   * @type {Object.<string, HTMLElement>}
   * @private
   */
  this.anchors_ = anchors;

  /**
   * This property holds the corner elements within which the corner anchors are located.
   * @type {Object.<string, HTMLElement>}
   * @private
   */
  this.corners_ = corners;
};


/**
 * @const
 * @type {string}
 * @private
 */
H.ui.Layout.cssClassPrefix_ = 'H_l_';


/**
 * This enumeration holds the CSS class names used for layout elements
 * @enum {string}
 */
H.ui.Layout.CssClassNames = {
  TOP: H.ui.Layout.cssClassPrefix_ + 'top',
  BOTTOM: H.ui.Layout.cssClassPrefix_ + 'bottom',
  LEFT: H.ui.Layout.cssClassPrefix_ + 'left',
  RIGHT: H.ui.Layout.cssClassPrefix_ + 'right',
  CENTER: H.ui.Layout.cssClassPrefix_ + 'center',
  MIDDLE: H.ui.Layout.cssClassPrefix_ + 'middle',
  HORIZONTAL: H.ui.Layout.cssClassPrefix_ + 'horizontal',
  VERTICAL: H.ui.Layout.cssClassPrefix_ + 'vertical',
  ANCHOR: H.ui.Layout.cssClassPrefix_ + 'anchor'
};


/**
 * This method updates the layout when items change within the layout containers.
 */
H.ui.Layout.prototype.update = function() {
  'use strict';

  var anchors = this.anchors_,
      Alignment = H.ui.LayoutAlignment;

  this.updateAnchor_(anchors[Alignment.TOP_CENTER], true);
  this.updateAnchor_(anchors[Alignment.BOTTOM_CENTER], true);
  this.updateAnchor_(anchors[Alignment.LEFT_MIDDLE], false);
  this.updateAnchor_(anchors[Alignment.RIGHT_MIDDLE], false);
};


/**
 * Updates an anchors alignment (for ...center anchors).
 * @param {Element} anchor the anchor element to update
 * @param {boolean} horizontal whether this anchor needs a horizontal update (otherwise vertical)
 * @private
 */
H.ui.Layout.prototype.updateAnchor_ = function(anchor, horizontal) {
  'use strict';

  var height,
      width;
  if (horizontal) {
    width = anchor.offsetWidth;
    anchor.style.marginLeft = (-Math.round(width * 0.5)) + 'px';
  } else {
    height = anchor.offsetHeight;
    anchor.style.marginTop = (-Math.round(height * 0.5)) + 'px';
  }
};


/**
 * This method moves a UI element to the specified alignment anchor within the layout.
 * @param {H.ui.Control} control the control to move
 * @param {H.ui.LayoutAlignment} alignment the the anchor's alignment to which to move the control
 */
H.ui.Layout.prototype.put = function(control, alignment) {
  'use strict';

  var controlEl = control ? control.getElement() : null,
      anchorEl = alignment ? this.anchors_[alignment] : null;
  if (controlEl && anchorEl) {
    if (controlEl.parentElement) {
      controlEl.parentElement.removeChild(controlEl);
    }

    anchorEl.insertBefore(
        controlEl,
        // Either the alignment ends with "bottom" or "right":
        /(bottom|right)$/.test(alignment) ||
        // Or it contains "center" or "middle" and has a even number of children:
        /(center|middle)/.test(alignment) && anchorEl.childNodes.length % 2 ?
        // Then we insert it at the first position:
        anchorEl.firstChild :
        // Otherwise at the end:
        null);
  }
  this.update();
};
