goog.provide('H.ui.ZoomControl');

goog.require('H.ui.Control');
goog.require('H.ui.base.Button');
goog.require('H.ui.icons.zoom');



/**
 * This class represents the UI controls for zooming in an out of the map.
 *
 * @constructor
 * @param {H.ui.ZoomControl.Options=} opt_options optional parameters to be passed to the map.
 *
 * @extends {H.ui.Control}
 * @export
 * @publish
 */
H.ui.ZoomControl = function(opt_options) {
  'use strict';

  var options = opt_options || {};
  H.ui.Control.call(this);

  this.onZoomBtnStateChange_ = H.lang.bind(this.onZoomBtnStateChange_, this);

  /**
   * @type {H.ui.base.Button}
   * @private
   */
  this.zoomInBtn_ = new H.ui.base.Button({
    'label': H.ui.icons.zoom['in'],
    'onStateChange': this.onZoomBtnStateChange_
  });

  /**
   * @type {H.ui.base.Button}
   * @private
   */
  this.zoomOutBtn_ = new H.ui.base.Button({
    'label': H.ui.icons.zoom.out,
    'onStateChange': this.onZoomBtnStateChange_
  });

  //add the buttons as this control's children
  this.addChild(this.zoomInBtn_);
  this.addChild(this.zoomOutBtn_);

  //add some css classes
  this.addClass('H_zoom');
  this.addClass('H_grp');

  //apply options or defaults
  this['setZoomSpeed'](options['zoomSpeed'] || 0.004);
  this.setAlignment(options['alignment'] || 'right-middle');
};
H.lang.inherits(H.ui.ZoomControl, H.ui.Control);


/**
 * @param {goog.events.Event} evt
 * @private
 */
H.ui.ZoomControl.prototype.onZoomBtnStateChange_ = function(evt) {
  'use strict';

  var target = evt.target,
      zoomDirection = (target === this.zoomInBtn_) ? 1 : -1,
      roundingFunction = (zoomDirection > 0) ? Math.ceil : Math.floor,
      map = this.map;

  if (map) {
    if (target.getState() === H.ui.base.Button.State.DOWN) {
      map.getViewModel().startControl();
      map.getViewModel().control(0, 0, zoomDirection * this.zoomSpeed_, 0, 0, 0);
    } else {
      map.getViewModel().endControl(true, function(endView) {
        endView['zoom'] = roundingFunction(endView.zoom);
      });
    }
  }
};


/**
 * @override
 * @export
 */
H.ui.ZoomControl.prototype.renderInternal = function(element, doc) {
  'use strict';
  H.ui.Control.prototype['renderInternal'].call(this, element, doc);
  this.zoomInBtn_.setTooltip(this.getLocalization().translate('zoom.in'));
  this.zoomOutBtn_.setTooltip(this.getLocalization().translate('zoom.out'));
};


/**
 * @override
 * @export
 */
H.ui.ZoomControl.prototype.setAlignment = function(alignment) {
  'use strict';

  H.ui.Control.prototype.setAlignment.call(this, alignment);

  var newPos = this.getAlignment(),
      Alignment = H.ui.LayoutAlignment,
      first,
      second;

  if (/^top/g.test(newPos) || /^bottom/g.test(newPos)) {
    first = this.zoomOutBtn_;
    second = this.zoomInBtn_;
  } else {
    first = this.zoomInBtn_;
    second = this.zoomOutBtn_;
  }
  this.removeChild(first);
  this.removeChild(second);
  this.addChild(first);
  this.addChild(second);
  return this;
};


/**
 * @override
 * @export
 */
H.ui.ZoomControl.prototype.onMapEngineTypeChange = function(engineType) {
  'use strict';
  var hide = engineType !== H.Map.EngineType.P2D;
  this.getElement().style.display = hide ? 'none' : '';
};


/**
 * This method returns the zoom speed (in levels per millisecond) which is applied when the button is pressed
 * constantly.
 * @return {number} the current zoom speed
 *
 * @export
 * @publish
 */
H.ui.ZoomControl.prototype.getZoomSpeed = function() {
  'use strict';

  return this.zoomSpeed_;
};


/**
 * This method sets the zoom speed (in levels per millisecond) which is applied when the button is pressed constantly.
 * @param {number} zoomSpeed the zoom speed
 *
 * @publish
 */
H.ui.ZoomControl.prototype['setZoomSpeed'] = function(zoomSpeed) {
  'use strict';

  this.zoomSpeed_ = zoomSpeed;
};


/**
 * @typedef {{
 *   zoomSpeed: (number|undefined),
 *   alignment: (H.ui.LayoutAlignment|undefined)
 * }}
 * @property {number=} zoomSpeed  the speed if zooming in and out in levels per millisecond, default is {@code 0.05}
 * @property {string} alignment the preferred layout alignment for this control
 * @publish
 */
H.ui.ZoomControl.Options;
