goog.provide('H.ui.LayoutAlignment');


/**
 * This enumeration holds the possible layout alignments for UI elements.
 * @enum {string}
 */
H.ui.LayoutAlignment = {
  'TOP_LEFT': 'top-left',
  'TOP_CENTER': 'top-center',
  'TOP_RIGHT': 'top-right',
  'LEFT_TOP': 'left-top',
  'LEFT_MIDDLE': 'left-middle',
  'LEFT_BOTTOM': 'left-bottom',
  'RIGHT_TOP': 'right-top',
  'RIGHT_MIDDLE': 'right-middle',
  'RIGHT_BOTTOM': 'right-bottom',
  'BOTTOM_LEFT': 'bottom-left',
  'BOTTOM_CENTER': 'bottom-center',
  'BOTTOM_RIGHT': 'bottom-right'
};


/**
 * Checks whether the passed alignment is supported
 * @param {string} alignment
 * @return {boolean}
 */
H.ui.validateAlignment = function(alignment) {
  'use strict';
  return typeof alignment === 'string' &&
      H.ui.LayoutAlignment[alignment.replace('-', '_').toUpperCase()] === alignment;
};


