goog.provide('H.ui.InfoBubble');

goog.require('H.ui.base.Element');
goog.require('H.ui.css');



/**
 * This class represents an information bubble bound to a geo-position on the map.
 *
 * @constructor
 * @param {H.geo.IPoint} position the geo-position to which this info bubble corresponds
 * @param {H.ui.InfoBubble.Options=} opt_options optional parameters to be passed to the info bubble
 * @extends {H.ui.base.Element}
 * @export
 * @publish
 */
H.ui.InfoBubble = function(position, opt_options) {
  'use strict';

  var options = opt_options || {};

  H.ui.base.Element.call(this, 'div', 'H_ib');

  /**
   * @type {function(Event)}
   * @private
   */
  this.onUserClose_ = H.lang.bind(function(evt) {
    if (!this.isDisabled()) {
      this.close();
      evt.preventDefault();
    }
  }, this);

  this.update = H.lang.bind(this.update, this);
  this.deferredUdate_ = H.lang.bind(this.deferredUdate_, this);
  this.setPosition(position);

  this.setContent(options['content']);
  if (options['onStateChange']) {
    this.addEventListener(H.ui.base.Element.EventType.STATECHANGE, options['onStateChange']);
  }
  this.setState(H.ui.InfoBubble.State['OPEN']);
};
H.lang.inherits(H.ui.InfoBubble, H.ui.base.Element);


/**
 * @type {H.Map}
 * @private
 */
H.ui.InfoBubble.prototype.map_ = null;


/**
 * This method returns the current geo-position of this info bubble.
 * @return {H.geo.Point} the current geo-position
 */
H.ui.InfoBubble.prototype.getPosition = function() {
  'use strict';

  return this.position_;
};


/**
 * This method sets the geo-position of this info bubble.
 * @param {H.geo.IPoint|H.geo.Point} position the new geo-position of this bubble
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.setPosition = function(position) {
  'use strict';

  this.position_ = H.geo.Point.fromIPoint(position);
  this.update();
};


/**
 * Sets the map to which the UI is attached
 * @param {H.Map} map
 */
H.ui.InfoBubble.prototype.setMap = function(map) {
  'use strict';
  this.map_ = map;
};


/**
 * @override
 * @export
 */
H.ui.InfoBubble.prototype.renderInternal = function(element, doc) {
  'use strict';

  this.bodyEl_ = H.ui.dom.mkEl(doc, 'div', 'H_ib_body');
  this['tailEl_'] = H.ui.dom.mkEl(doc, 'div', 'H_ib_tail', H.ui.InfoBubble.TAIL_SVG_);
  this.closeEl_ = H.ui.dom.mkEl(doc, 'div', 'H_ib_close', H.ui.InfoBubble.CLOSE_SVG_);
  this.contentEl_ = H.ui.dom.mkEl(doc, 'div', 'H_ib_content', '&nbsp;');
  this.bodyEl_.appendChild(this.closeEl_);
  this.bodyEl_.appendChild(this.contentEl_);

  H.ui.dom.on(this.closeEl_, 'end', this.onUserClose_);
  this.map_.getViewModel().addEventListener('sync', this.update);
  this.map_.getViewPort().addEventListener('sync', this.update);
  this.map_.addEventListener('enginechange', this.deferredUdate_);

  element.appendChild(this.bodyEl_);
  element.appendChild(this['tailEl_']);
  this.setContent(this.content_);
};


/**
 * A helper method to trigger a deferrred update() call.
 * It's necessary to assure that the browser has time to render the bubble's content element and updates the offsetWidth
 * @private
 */
H.ui.InfoBubble.prototype.deferredUdate_ = function() {
  if (!this.deferredUdateHandle_) {
    this.deferredUdateHandle_ = setTimeout(this.update, 0);
  }
};


/**
 * The handle to an remained deferred update call
 * @private
 * @type {number}
 */
H.ui.InfoBubble.prototype.deferredUdateHandle_;


/**
 * This method updates the position of the bubble on the screen.
 */
H.ui.InfoBubble.prototype.update = function() {
  'use strict';

  var el = this['el'],
      map = this.map_,
      vpWidth,
      vpHeight,
      screenCoord,
      x,
      y,
      overlap,
      width;

  this.deferredUdateHandle_ = 0;

  if (map && this.getElement() && this.getState() == H.ui.InfoBubble.State['OPEN']) {
    screenCoord = map.geoToScreen(this.position_);

    x = screenCoord && screenCoord['x'];
    y = screenCoord && screenCoord['y'];

    vpWidth = map.getViewPort()['width'],
    vpHeight = map.getViewPort()['height'];

    if (map.getEngine().type !== H.Map.EngineType.P2D) {
      el.style.display = 'none';
    } else {
      if (x < -vpWidth || x > 2 * vpWidth || y < -vpWidth || y > 2 * vpWidth) {
        el.style.display = 'none';
      } else {
        el.style.display = '';
        if (H.ui.util.isStockAndroid) {
          el.style.left = x - vpWidth + 'px';
          el.style.top = y + 'px';
        } else {
          H.ui.css.translate(el, x, y);
        }
        width = this.contentEl_.offsetWidth;
        overlap = (x - width);
        if (overlap < 40) {
          this.setContentOffset_(-overlap, width);
        }
      }
    }
  }
};


/**
 * Updates this bubble left-right wobbliness.
 * @param {number} overlap the overlap between the bubble and the left screen border
 * @param {number=} opt_contentWidth the width of the content
 * @private
 */
H.ui.InfoBubble.prototype.setContentOffset_ = function(overlap, opt_contentWidth) {
  'use strict';

  var contentWidth = opt_contentWidth || this.contentEl_.offsetWidth,
      tailWidth = this['tailEl_'].offsetWidth,
      correctedX = Math.max(0, Math.min(contentWidth - tailWidth, overlap));
  this.bodyEl_.style.right = correctedX ? -correctedX + 'px' : '0';
};


//#################################################  STATE  ############################################################
/**
 * This enumeration holds the state an info bubble can have.
 * @enum {string}
 * @export
 * @publish
 */
H.ui.InfoBubble.State = {
  /**
   * This value represents the state where an info bubble is open and visible (value: 'open').
   */
  'OPEN': 'open',
  /**
   * This value represents the state where an info bubble is closed and invisible (value: 'closed')
   */
  'CLOSED': 'closed'
};


/**
 * The current state (OPEN / CLOSED) of this info bubble
 * @private
 * @type {H.ui.InfoBubble.State}
 */
H.ui.InfoBubble.prototype.state_ = H.ui.InfoBubble.State['OPEN'];


/**
 * This method returns this info bubbles current state.
 * @return {H.ui.InfoBubble.State} this bubble's current state
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.getState = function() {
  return this.state_;
};


/**
 * This method sets the info bubble's state.
 * @param {H.ui.InfoBubble.State} state the new state
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.setState = function(state) {
  'use strict';

  if (state !== this.state_) {
    this.state_ = state;
    this.dispatchEvent(H.ui.base.Element.EventType.STATECHANGE);
  }
  if (this.getElement()) {
    if (this.state_ === H.ui.InfoBubble.State['OPEN']) {
      this.getElement().style.display = 'block';
      this.deferredUdate_();
    } else {
      this.getElement().style.display = 'none';
    }
  }
};


/**
 * This method closes the info bubble (setting its state to CLOSED)
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.close = function() {
  'use strict';

  this.setState(H.ui.InfoBubble.State['CLOSED']);
};


/**
 * This method opens the info bubble (setting its state to OPEN)
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.open = function() {
  'use strict';

  this.setState(H.ui.InfoBubble.State['OPEN']);
};


//################################################  CONTENT  ###########################################################
/**
 * This method returns the bubble's content element.
 *
 * Note: Before adding an info bubble to a UI object the content element is null.
 *
 * @return {?HTMLElement} the content element of this info bubble
 *
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.getContentElement = function() {
  'use strict';

  return this.contentEl_;
};


/**
 * This methods sets the content of the info bubble. This can either be a string (applied as innerHTML) to
 * the content element of this info bubble or a HTML node which is appended to the content element.
 * @param {string|Node} content the content for this bubble
 *
 * @export
 * @publish
 */
H.ui.InfoBubble.prototype.setContent = function(content) {
  'use strict';

  var contentType = 0;

  // validate content
  if (content) {
    if (typeof content === 'string' || (typeof content.nodeType === 'number' && typeof content.nodeName === 'string')) {
      this.content_ = content;
    } else {
      throw new Error('InfoBubble content must be a string or HTML node.');
    }
  } else {
    this.content_ = undefined;
  }

  if (this.getElement()) {
    if (this.content_) {
      this.contentEl_.innerHTML = '';
      if (typeof this.content_ === 'string') {
        this.contentEl_.innerHTML = content;
      } else if (typeof content.nodeType === 'number' && typeof content.nodeName === 'string') {
        this.contentEl_.appendChild(content);
      }
    } else {
      // the bubble does not have content
      this.contentEl_.innerHTML = '&nbsp;';
    }
    this.deferredUdate_();
  }
};


/**
 * @override
 */
H.ui.InfoBubble.prototype.disposeInternal = function() {
  'use strict';

  this.close();
  this.setContent(null);

  if (this.closeEl_) {
    H.ui.dom.off(this.closeEl_, 'end', this.onUserClose_);
  }

  if (this.map_) {
    this.map_.getViewModel().removeEventListener('sync', this.update);
    this.map_.getViewPort().removeEventListener('sync', this.update);
  }
  H.ui.base.Element.prototype.disposeInternal.call(this);
};


/**
 * The SVG for the bubble's tail
 * @private
 */
H.ui.InfoBubble.TAIL_SVG_ =
    '<svg viewBox="0 0 25 25">' +
    '<g>' +
    '<path d="M 11.3,0.23 5.08,12.1 C 2.28,12.2 0,13.2 0,14.5 0,15.9 2.28,17 5.08,17 c 2.8,0 5.12,-1.1 5.12,-2.5 0' +
    ',-0.6 -0.62,-1.2 -1.52,-1.7 L 20.9,1.13 21,0.23 l -9.7,0 z" style="fill:#0d2e41;fill-opacity:0.3" />' +
    '<path d="M 19.9,0.266 4.88,14.8 12.5,0.266 z" />' +
    '</g>' +
    '</svg>';


/**
 * The SVG for the bubble's close button
 * @private
 */
H.ui.InfoBubble.CLOSE_SVG_ =
    '<svg version="1.1" class="H_icon" viewBox="0 0 12 22">' +
    '<path d="M 9.01,17 6.04,14 3,17 0.03,14.1 3,11 0.096,8.17 3.07,5.15 6.04,8.07 8.95,5.15 11.9,8.17 9.01,11 12,14' +
    '.1 9.01,17" />' +
    '</svg>';


/**
 * @typedef {{
 *   onStateChange: (function(H.ui.InfoBubble.State)|undefined),
 *   content: (string|Node|undefined)
 * }}
 * @property {function(H.util.Event)=} onStateChange a callback to be invoked when the info bubble's state
 *  changes
 * @property {(string|Node)} content content to be added to the info bubble
 * @publish
 */
H.ui.InfoBubble.Options;


/**
 * Disposes of this InfoBubble instance.
 *
 * Note: Disposing of an info bubble does not automatically remove it from the UI's info bubble collection.
 * Please call {@link H.ui.UI#removeBubble} before disposing of an info bubble instance.
 *
 * @method H.ui.InfoBubble#dispose
 * @publish
 */


/**
 * This event is dispatched when state of the bubble has changed
 * @event H.ui.InfoBubble#statechange
 * @type {H.util.Event}
 * @publish
 */
