goog.provide('H.ui.util.VendorPrefix');



/**
 * This class encapsulates functionality for setting vendor-prefixed CSS property names on 'style' objects.
 * @constructor
 */
H.ui.util.VendorPrefix = function() {
  'use strict';

  /**
   * @dict
   * @private
   */
  this.knownProperties_ = {};

  /**
   * @type {string}
   * @private
   */
  this.probablePrefix_ = '';

  /**
   * @type {Object}
   * @private
   */
  this.testStyle_ = document.createElement('span').style;
};


/**
 * @type {Array.<string>}
 * @private
 */
H.ui.util.VendorPrefix.LIST_JS_ = ['O', 'Ms', 'ms', 'Moz', 'Webkit'];


/**
 * This method tries to find the correct name for an unprefixed CSS property. It tries to do the following:
 *
 *  1) It always checks for the availability of the unprefixed property first
 *  2) If a prefix was previously discovered, it will check if the current property can be prefixed with
 *     the same prefix
 *  3) It will check all known vendor prefixes
 *
 * If none of the above succeeds the method will throw an error.
 *
 * @param {string} property the unprefixed name of the CSS style property
 * @return {string} the property name to be used in the current environment
 * @throws {Error} This method throws an error if no variant of the unprefixed property could be found
 */
H.ui.util.VendorPrefix.prototype.getPropertyName = function(property) {
  'use strict';
  var name = this.knownProperties_[property],
      found = !!name;

  if (!name) {
    if (property in this.testStyle_) {
      // if the unprefixed name is in the style, we can store and return the name
      name = this.knownProperties_[property] = property;
      //console.log("vprefix", name);
    } else if ((name = this.detect_(property))) {
      // otherwise we try to detect the prefix, store it and return it
      //console.log("vprefix", name);
      this.knownProperties_[property] = name;
    } else {
      // could not detect it, throw an error
      throw new Error('Could not find any variant of CSS property [' + property + ']');
    }
  }
  return name;
};


/**
 * This method detects the vendor prefix for the provided unprefixed property name.
 * @param {string} property the unprefixed name of the CSS style property
 * @return {?string} the property name to be used in the current environment or null if no prefix applies
 * @private
 */
H.ui.util.VendorPrefix.prototype.detect_ = function(property) {
  'use strict';
  var list = H.ui.util.VendorPrefix.LIST_JS_,
      i = list.length,
      prefix = '',
      capitalizedProp = property.substr(0, 1).toUpperCase() + property.substr(1),
      prefixedProp = '',
      found = false;

  //If we had a previous detection, we can assume that a prefixed property will have the same prefix. We will check this
  //first...
  if (this.probablePrefix_) {
    prefixedProp = this.probablePrefix_ + capitalizedProp;
    found = (prefixedProp in this.testStyle_);
  }

  // If we have not yet found anything, we will iterate over the list of prefixes until we find the correct one.
  while (i-- && !found) {
    prefix = list[i];
    prefixedProp = prefix + capitalizedProp;
    found = (prefixedProp in this.testStyle_);
  }

  // When we found a prefix, we will store it as the probable prefix for the next detection.
  if (found) {
    this.probablePrefix_ = prefix;
  }

  return found ? prefixedProp : null;
};
