goog.provide('H.ui.Control');

goog.require('H.ui.LayoutAlignment');
goog.require('H.ui.base.Container');
//goog.require('H.ui.i18n.ILocalizable');



/**
 * This class represents the base class for UI controls on the map.
 *
 * @constructor
 * @extends {H.ui.base.Container}
 * @export
 * @publish
 */
H.ui.Control = function() {
  'use strict';

  H.ui.base.Container.call(this, 'div', 'H_ctl');

  /**
   * @protected
   * @type {H.Map}
   */
  this.map = null;


  /**
   * @type {!H.ui.LayoutAlignment}
   * @private
   */
  this.alignment_ = H.ui.LayoutAlignment.TOP_LEFT;

};
H.lang.inherits(H.ui.Control, H.ui.base.Container);


/**
 * @protected
 * @type {H.ui.i18n.Localization}
 */
H.ui.Control.prototype.locale;


/**
 * This property holds this controls container element once it is rendered into a UI.
 * @property {HTMLElement} H.ui.Control#el
 * @publish
 */
H.ui.Control.prototype['el'] = null;


/**
 * Sets the map to which the UI is attached
 * @param {H.Map} map
 */
H.ui.Control.prototype.setMap = function(map) {
  'use strict';
  this.map = map;
};


/**
 * This method sets locale for the current control. Not exported! only
 * for UI class use
 * @param {H.ui.i18n.Localization} localization
 */
H.ui.Control.prototype.setLocalization = function(localization) {
  'use strict';
  this.localization = localization;
};


/**
 * Renders this control into a map UI object.
 * @param {Document} doc the document into which this control renders.
 * @return {Element} the fully rendered HTML representation of this control
 */
H.ui.Control.prototype.render = function(doc) {
  'use strict';
  return H.ui.base.Container.prototype.render.call(this, doc);
};


/**
 * @override
 * @export
 */
H.ui.Control.prototype.renderInternal = function(element, doc) {
  'use strict';

  H.ui.base.Container.prototype['renderInternal'].call(this, element, doc);

  this.setAlignment(this.getAlignment());
};


/**
 * This abstract method can be overridden by deriving classes to be invoked when the UI object's unit system
 * changes.
 * @param {H.ui.UnitSystem} unitSystem the unit system the UI currently uses
 * @export
 * @publish
 */
H.ui.Control.prototype.onUnitSystemChange = function(unitSystem) {};


/**
 * This abstract method can be overridden by deriving classes to be invoked when the underlying map engine
 * changes.
 * @param {H.Map.EngineType} engineType the engine type the map currently uses
 * @export
 * @publish
 */
// TODO: Add generic update for all sorts of state changes, rather than adding methods
// for each possible state change
H.ui.Control.prototype.onMapEngineTypeChange = function(engineType) {};


/**
 * This method returns the map to which this control is attached.
 *
 * Note: Before adding a control to a UI object this method returns null.
 *
 * @return {?H.Map} return the map
 * @export
 * @publish
 */
H.ui.Control.prototype.getMap = function() {
  'use strict';
  return this.map;
};


/**
 * This method returns the localization object which corresponds to the UI's current
 * locale.
 *
 * @return {H.ui.i18n.Localization}
 * @export
 * @publish
 */
H.ui.Control.prototype.getLocalization = function() {
  'use strict';
  return this.localization;
};


/**
 * This method returns this control's layout alignment.
 * @return {string} the control's current layout alignment
 * @export
 * @publish
 */
H.ui.Control.prototype.getAlignment = function() {
  'use strict';
  return this.alignment_;
};


/**
 * This method sets the control's layout alignments.
 *
 * The possible alignments are as follows within the map view port:
 * <pre>
 * +--------------------------------------------------------------------------------+
 * |  "top-left"  > >           &lt; &lt;  "top-center"  > >            &lt; &lt;  "top-right"  |
 * |  "lef-ttop"                                                       "right-top"  |
 * |  v                                                                          v  |
 * |  v                                                                          v  |
 * |                                                                                |
 * |                                                                                |
 * |  ^                                                                          ^  |
 * |  ^                                                                          ^  |
 * |  "left-middle"                                                 "right-middle"  |
 * |  v                                                                          v  |
 * |  v                                                                          v  |
 * |                                                                                |
 * |                                                                                |
 * |  ^                                                                          ^  |
 * |  ^                                                                          ^  |
 * |  "left-bottom"                                                 "right-bottom"  |
 * |  "bottom-left"  > >       &lt; &lt;  "bottom-center"  > >       &lt; &lt;  "bottom-right"  |
 * +--------------------------------------------------------------------------------+
 * </pre>
 *
 * Elements with the same layout alignments are lined up in the directions of the arrows (see the illustration above).
 * @publish
 * @export
 * @param {string} alignment the new alignment of the control
 * @return {H.ui.Control} returns this control instance
 */
H.ui.Control.prototype.setAlignment = function(alignment) {
  'use strict';
  if (H.ui.validateAlignment(alignment)) {
    this.alignment_ = /** @type {H.ui.LayoutAlignment} */(alignment);
    this.dispatchEvent('alignmentchange');
  }
  return this;
};


/**
 * This event is fired when control is realigned
 * @event H.ui.Control#alignmentchange
 * @type {H.util.Event}
 * @publish
 */
