goog.provide('H.ui.panorama.Discovery');

goog.require('goog.events.EventTarget');
goog.require('H.ui.panorama.Miniman');



/**
 * This class encapsulates the functionality for discovering entry points for
 * panorama. It is used by the Pano control.
 *
 *
 * There are two different behaviors for the discovery:
 * HOVER:
 *  A small icon (miniman) is attached to the pointer indicating whether street level is available at the pointer
 *  position. If street level is available the miniman turns blue and a ellipse is displayed at the next coordinate
 *  of the closes panorama entry point. Additionally, the discovery object will try to load a preview and display it
 *  in an info bubble. Tapping on the ellipse or the map will trigger an entry request.
 *
 * TAP:
 *  When tapping on the map the discovery object will not load a preview and will instead immediately fire an
 *  entry request.
 *
 * @param {H.ui.UI} ui
 * @constructor
 * @extends {goog.events.EventTarget}
 */
H.ui.panorama.Discovery = function(ui) {
  'use strict';

  goog.events.EventTarget.call(this);

  this.ui = ui;
  this.map = ui.getMap();

  // HOVER BEHAVIOR:
  this.lastPointerX_ = Number.MAX_VALUE;
  this.lastPointerY_ = Number.MAX_VALUE;

  this.onPointerMove_ = H.lang.bind(this.onPointerMove_, this);
  this.onPointerStop_ = H.lang.bind(this.onPointerStop_, this);
  this.onPointerPanorama_ = H.lang.bind(this.onPointerPanorama_, this);

  this.onMapViewChange_ = H.lang.bind(this.onMapViewChange_, this);
  this.onMapViewChangeStart_ = H.lang.bind(this.onMapViewChangeStart_, this);
  this.onMapViewChangeEnd_ = H.lang.bind(this.onMapViewChangeEnd_, this);

  this.onPointerEnter_ = H.lang.bind(this.onPointerEnter_, this);
  this.onPointerLeave_ = H.lang.bind(this.onPointerLeave_, this);

  this.miniman_ = new H.ui.panorama.Miniman();
  this.miniman_.render(ui.getElement().ownerDocument);

  this.enterOnTap_ = H.lang.bind(this.enterOnTap_, this);

  H.ui.dom.on(this.miniman_.circleEl['el'], 'start', this.enterOnTap_);


  // TAP BEHAVIOR
  this.lastTapX_ = Number.MAX_VALUE;
  this.lastTapY_ = Number.MAX_VALUE;
  this.onTapPanorama_ = H.lang.bind(this.onTapPanorama_, this);
  this.onTap_ = H.lang.bind(this.onTap_, this);

  // BUBBLE
  this.infoBubble_ = new H.ui.InfoBubble(this.map.getCenter());
  this.infoBubble_.addClass('H_ib_noclose H_pano_ib');

  this.enabled_ = false;

  /**
   * Counter for tap events, first tap shows infobubble, second tap dives into panorama
   * @type {number}
   * @private
   */
  this.tapCounter_ = 0;
};
H.lang.inherits(H.ui.panorama.Discovery, goog.events.EventTarget);


/**
 * This method enables the discovery object
 */
H.ui.panorama.Discovery.prototype.enable = function() {
  'use strict';
  var map = this.map,
      that = this;
  if (!this.enabled_) {
    this.ui.getElement().insertBefore(this.miniman_['el'], this.ui.getElement().firstChild);
    this.ui.addBubble(this.infoBubble_);
    this.infoBubble_.close();

    H.ui.dom.on(this.infoBubble_['el'], 'start', this.enterOnTap_);

    map.addEventListener('pointermove', this.onPointerMove_);
    map.addEventListener('drag', this.onPointerMove_);
    map.addEventListener('tap', this.onTap_);
    map.addEventListener('mapviewchange', this.onMapViewChange_);
    map.addEventListener('mapviewchangestart', this.onMapViewChangeStart_);
    map.addEventListener('mapviewchangeend', this.onMapViewChangeEnd_);
    map.addEventListener('pointerleave', this.onPointerLeave_);
    map.addEventListener('pointerenter', this.onPointerEnter_);

    this.enabled_ = true;
  }
};


/**
 * Hide pointer and preview infobubble when pointer leaves UI container
 * @param {H.mapevents.Event} evt the event
 * @private
 */
H.ui.panorama.Discovery.prototype.onPointerLeave_ = function(evt) {
  'use strict';
  if (evt['currentPointer']['type'] !== 'mouse') return;
  this.miniman_.remove();
  this.ui.removeBubble(this.infoBubble_);
};


/**
 * Show pointer and preview infobubble when pointer enters UI container
 * @param {H.mapevents.Event} evt the event
 * @private
 */
H.ui.panorama.Discovery.prototype.onPointerEnter_ = function(evt) {
  'use strict';
  if (evt['currentPointer']['type'] !== 'mouse') return;
  this.ui.getElement().insertBefore(this.miniman_['el'], this.ui.getElement().firstChild);
  this.ui.addBubble(this.infoBubble_);
  H.ui.dom.on(this.infoBubble_['el'], 'start', this.enterOnTap_);
  this.infoBubble_.close();
};


/**
 * Method enables panorama entering when info bubble was clicked
 & @private
 */
H.ui.panorama.Discovery.prototype.enterOnTap_ = function() {
  'use strict';
  this.tryEnter(this.lastPointerX_, this.lastPointerY_);
};


/**
 * tolerance for the second tap, that we consider it as a tap on the same point
 * @type {number}
 * @private
 */
H.ui.panorama.Discovery.prototype.tapTolerance_ = 25;


/**
 * This method disables the discovery object
 */
H.ui.panorama.Discovery.prototype.disable = function() {
  'use strict';

  var bubbleEl = this.infoBubble_['el'],
      map = this.map;

  this.miniman_.remove();
  this.ui.removeBubble(this.infoBubble_);

  bubbleEl && H.ui.dom.off(bubbleEl, 'start', this.enterOnTap_);
  H.ui.dom.off(this.miniman_.circleEl['el'], 'start', this.enterOnTap_);

  map.removeEventListener('pointermove', this.onPointerMove_);
  map.removeEventListener('drag', this.onPointerMove_);
  map.removeEventListener('tap', this.onTap_);
  map.removeEventListener('mapviewchange', this.onMapViewChange_);
  map.removeEventListener('mapviewchangestart', this.onMapViewChangeStart_);
  map.removeEventListener('mapviewchangeend', this.onMapViewChangeEnd_);
  map.removeEventListener('pointerleave', this.onPointerLeave_);
  map.removeEventListener('pointerenter', this.onPointerEnter_);
  this.enabled_ = false;
};


/**
 * This method request to enter street level immediately.
 * @param {number} x the viewport x-coordinate
 * @param {number} y the viewport y-coordinate
 */
H.ui.panorama.Discovery.prototype.tryEnter = function(x, y) {
  'use strict';

  if (H.map.render.panorama) {
    this.lastTapX_ = x;
    this.lastTapY_ = y;
    H.map.render.panorama.RenderEngine.getClosestPanoramas(
        this.map.screenToGeo(this.lastTapX_, this.lastTapY_),
        this.onTapPanorama_);
  }
};


/**
 * Resets the 'pointerStop' handle and cancels the preview image loading (in case any has been scheduled).
 * @private
 */
H.ui.panorama.Discovery.prototype.reset_ = function() {
  'use strict';
  if (this.stopTimeout_) {
    clearTimeout(this.stopTimeout_);
  }
  if (this.previewHandle_) {
    this.previewHandle_.cancel();
  }
  this.lastPanoramaPosition_ = null;
};


/**
 * Handles 'pointermove' events on the map
 * @param {H.mapevents.Event} evt the event
 * @private
 */
H.ui.panorama.Discovery.prototype.onPointerMove_ = function(evt) {
  'use strict';
  var currentPointer = evt['currentPointer'],
      pointerX,
      pointerY;

  if (this.map.getEngine().type === H.Map.EngineType.P2D) {
    if (currentPointer.type !== 'touch') {
      pointerX = currentPointer['viewportX'];
      pointerY = currentPointer['viewportY'];
      // TODO: there must be a better way to solve mouseout-pointermove problem for FF
      if (this.lastPointerX_ !== pointerX || this.lastPointerY_ !== pointerY) {
        this.miniman_.minimanEl['el'].style.display = 'block';

        this.reset_();
        this.lastPointerX_ = pointerX;
        this.lastPointerY_ = pointerY;

        this.miniman_.setScreenPosition(this.lastPointerX_, this.lastPointerY_);
        this.stopTimeout_ = setTimeout(this.onPointerStop_, 350);

        // disable miniman and close current info bubble
        this.miniman_.setPanorama(false);
        this.infoBubble_.close();
      }
    }
  }
};


/**
 * Executes the panorama check and preview loading.
 * @private
 */
H.ui.panorama.Discovery.prototype.onPointerStop_ = function() {
  'use strict';
  if (H.map.render.panorama) {
    H.map.render.panorama.RenderEngine.getClosestPanoramas(
        this.map.screenToGeo(this.lastPointerX_, this.lastPointerY_),
        this.onPointerPanorama_
    );
  }
};


/**
 * Handles a panorama response for the 'hover' use case (displaying info bubble and
 * setting miniman circle).
 * @param {Object} response
 * @private
 */
H.ui.panorama.Discovery.prototype.onPointerPanorama_ = function(response) {
  'use strict';
  var that = this,
      screenPos,
      evt;
  if (response && response.length > 0) {
    screenPos = this.map.geoToScreen(response[0].position);
    this.lastPanoramaPosition_ = response[0].position;
    this.miniman_.setPanorama(true, screenPos['x'], screenPos['y']);
    this.previewHandle_ = H.net.images.load(
        H.map.render.panorama.RenderEngine.getImageUrl(response[0]),
        function(image) {
          that.infoBubble_.setPosition(response[0].position);
          that.infoBubble_.setContent('<img src="' + image.src + '" />');
          that.infoBubble_.open();
        });
  } else if (this.tapCounter_) {
    this.onTapPanorama_(response);
  }
};


/**
 * Disable css transition on mapviewchangestart, so circle snaps nicely to current
 * panorama position (if one is available)
 * @private
 */
H.ui.panorama.Discovery.prototype.onMapViewChangeStart_ = function() {
  this.miniman_.circleEl.addClass('H_pano_notransition');
  this.infoBubble_.close();
};


/**
 * Enable css transition, circle animates to the panorama position
 * @private
 */
H.ui.panorama.Discovery.prototype.onMapViewChangeEnd_ = function() {
  this.miniman_.circleEl.removeClass('H_pano_notransition');
};


/**
 * Updates circle position if panorama is available.
 * @private
 */
H.ui.panorama.Discovery.prototype.onMapViewChange_ = function() {
  'use strict';
  if (this.lastPanoramaPosition_) {
    var screenPos = this.map.geoToScreen(this.lastPanoramaPosition_);
    screenPos && this.miniman_.setPanorama(true, screenPos['x'], screenPos['y']);
  }
  this.resetTapCounter_();
};


// TAP USE CASE
/**
 * This function handles a tap on the map, which should take the user to street level if panorama is available or
 * display an info bubble with an error message if not.
 *
 * @param {H.mapevents.Event} evt the tap event
 * @private
 */
H.ui.panorama.Discovery.prototype.onTap_ = function(evt) {
  'use strict';
  var currentPointer = evt['currentPointer'],
      isMouse = currentPointer['type'] === 'mouse',
      viewportX = currentPointer['viewportX'],
      viewportY = currentPointer['viewportY'],
      tolerance = this.tapTolerance_,
      abs = Math.abs;

  if (this.tapCounter_ === 1 &&
      (abs(this.lastPointerX_ - viewportX) < tolerance && abs(this.lastPointerY_ - viewportY) < tolerance)) {
    this.tapCounter_++;
  } else if (this.tapCounter_ === 0) {
    this.tapCounter_++;
  }

  // first tap sequence is requred only on touch devices
  if (this.tapCounter_ === 1 && !isMouse) {
    this.infoBubble_.close();
    this.lastPointerX_ = viewportX;
    this.lastPointerY_ = viewportY;
    this.lastTapX_ = viewportX;
    this.lastTapY_ = viewportY;
    this.onPointerStop_();
  }

  // enter panorama only if it's second tap if it's mouse tap enter panorama anyway
  if (this.tapCounter_ === 2 || isMouse) {
    this.tryEnter(viewportX, viewportY);
    this.resetTapCounter_();
  }
};


/**
 * Method resets tap counter.
 * @private
 */
H.ui.panorama.Discovery.prototype.resetTapCounter_ = function() {
  'use strict';
  this.tapCounter_ = 0;
};


/**
 * Handles a panorama response after tap (no preview, enter straight away).
 * @param {Object} response
 * @private
 */
H.ui.panorama.Discovery.prototype.onTapPanorama_ = function(response) {
  'use strict';
  var that = this,
      pos,
      evt;
  if (response && response.length > 0) {
    this.dispatchEvent({ type: 'enter', position: response[0].position });
  } else {
    pos = this.map.screenToGeo(this.lastTapX_, this.lastTapY_);
    this.infoBubble_.setPosition(pos);
    this.infoBubble_.setContent(this.ui.getLocalization().translate('panorama.unavailableHere'));
    this.infoBubble_.open();
  }
};
