goog.provide('H.ui.util.Events');



/**
 * This class encapsulates DOM event listener functionality across different event systems (touch, mouse, MS pointer
 * events). Instead of the individual event names this class translates abstract event names to the underlying
 * event names:
 *
 * * start: mousedown, touchstart, pointerdown
 * * end: mouseup, touchend, pointerup
 * * move: mousemove, touchmove, pointermove
 * * cancel: touchcancel, pointercancel
 * * over: mouseover, pointerover
 * * out: mouseout, pointerout
 * * hover: mousehover, pointerhover
 *
 * @constructor
 * @param {Array.<H.ui.util.Events.EventSet>=} opt_eventSets an optional array of event sets to use
 */
H.ui.util.Events = function(opt_eventSets) {
  'use strict';

  if (opt_eventSets) {
    this.sets_ = opt_eventSets;
  } else {
    this.sets_ = this.detectEventSets();
  }
};


/**
 * This method infers which events to listen for from the browser environment.
 *
 * @return {Array.<H.ui.util.Events.EventSet>}
 *
 * @export
 * @publish
 */
H.ui.util.Events.prototype.detectEventSets = function() {
  'use strict';

  var win = Function('return this;')(),
      nav = win.navigator,
      sets = [];
  if (nav) {
    //if we have a navigator object (we are in some kind of browser)...
    if (nav.msPointerEnabled) {
      // ...and if the navigator says it is msPointerEnabled (IE9 & IE10)
      // we use MSPOINTER events
      sets.push(H.ui.util.Events.MSPOINTER);
    } else if (nav.pointerEnabled) {
      // ... or if the navigator say it is pointerEnabled (IE11)
      // we use POINTER events
      sets.push(H.ui.util.Events.POINTER);
    }
  }

  if (sets.length < 1) {
    //if we have not detected any event sets yet...
    if ('TouchEvent' in win) {
      // ...and if there is a Touch object in the global namespace
      // we will listen to touch events
      sets.push(H.ui.util.Events.TOUCH);
    }
    // We also listen to mouse events on anything that is not pointer enabled
    // in addition to touch events since we never know whether a touch device
    // is also able to have mouse events
    sets.push(H.ui.util.Events.MOUSE);
  }

  return sets;
};


/**
 * This method adds event listeners to the provided element. It uses the event sets which are
 * currently registered in this event helper.
 * @param {Element} el the element to which to append the event listener
 * @param {string} type the event type to listen for
 * @param {function(Event)} listener the listener function
 * @param {boolean=} opt_useCapture optional flag to indicate whether to listen in the capture phase or
 *  the bubbling phase
 */
H.ui.util.Events.prototype.addEventListener = function(el, type, listener, opt_useCapture) {
  'use strict';

  var sets = this.sets_,
      eventName,
      i = sets.length;
  while (i--) {
    if ((eventName = sets[i][type])) {
      el.addEventListener(eventName, listener, opt_useCapture || false);
    }
  }
};


/**
 * This method removes previously added event listeners from the provided element. It uses the event sets which are
 * currently registered in this event helper.
 * @param {Element} el the element from which to remove the event listener
 * @param {string} type the event type for which to remove the listener
 * @param {function(Event)} listener the listener function to be remove
 * @param {boolean=} opt_useCapture optional flag indicating whether the listener was added to the capture
 *  or bubbling phase
 */
H.ui.util.Events.prototype.removeEventListener = function(el, type, listener, opt_useCapture) {
  'use strict';

  var sets = this.sets_,
      eventName,
      i = sets.length;
  while (i--) {
    if ((eventName = sets[i][type])) {
      el.removeEventListener(eventName, listener, opt_useCapture || false);
    }
  }
};


/**
 * @typedef {Object.<string, string>}
 */
H.ui.util.Events.EventSet;


/**
 * This event set can be used in pointer enabled environments such as IE11
 * @type {H.ui.util.Events.EventSet}
 * @const
 */
H.ui.util.Events.POINTER = {
  'start': 'pointerdown',
  'end': 'pointerup',
  'move': 'pointermove',
  'cancel': 'pointercancel',
  'over': 'pointerover',
  'out': 'pointerout',
  'hover': 'pointerhover'
};


/**
 * This event set can be used in MS-pointer enabled environments such as IE9 and IE10
 * @type {H.ui.util.Events.EventSet}
 * @const
 */
H.ui.util.Events.MSPOINTER = {
  'start': 'MSPointerDown',
  'end': 'MSPointerUp',
  'move': 'MSPointerMove',
  'cancel': 'MSPointerCancel',
  'over': 'MSPointerOver',
  'out': 'MSPointerOut',
  'hover': 'MSPointerHover'
};


/**
 * This event set can be used in touch-capable environments.
 * @type {H.ui.util.Events.EventSet}
 * @const
 */
H.ui.util.Events.TOUCH = {
  'start': 'touchstart',
  'end': 'touchend',
  'move': 'touchmove',
  'cancel': 'touchcancel'
};


/**
 * This event set can be used in mouse-enabled environments
 * @type {H.ui.util.Events.EventSet}
 * @const
 */
H.ui.util.Events.MOUSE = {
  'start': 'mousedown',
  'end': 'mouseup',
  'move': 'mousemove',
  'over': 'mouseover',
  'out': 'mouseout',
  'hover': 'mousehover'
};
